// Style
import classes from './MemberDebitMemosToValidateTile.module.scss'
import ViewAll from '../../ViewAll2/ViewAll'

// Utilities
import moment from 'moment'

// Config
// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  today: {
    start: moment()
      .startOf('day')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  },
  week: {
    start: moment()
      .startOf('day')
      .subtract(1, 'weeks')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  },
  month: {
    start: moment()
      .subtract(1, 'months')
      .startOf('day')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  }
}

