const MemberDebitMemosToValidateTile = props => {
  // Constants
  const { services = () => {} } = props
  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    range: [],
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})

  // Initialize
  React.useEffect(() => {
    fetchData({})
    fetchTileData({ range: rangeDate['month'] })
  }, [])

  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      range: list.range,
      ...params
    }
    setParams(_params)
    services.getDebitMemosForApproval({ entityType: 'Member' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      // setList(i => ({ ...i, body: results, count, limit, page }))
    })
  }

  const fetchTileData = params => {
    services.getDebitMemosForApproval({ entityType: 'Member' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      // setTileList(i => ({ ...i, body: results, count, limit, page }))
    })
  }

  // const onFilterChange = value => {
  //   fetchTileData({ range: rangeDate[value] })
  // }

  return (
    <React.Fragment>
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Member Debit Memos To Validate'}
        isFilterShown
        filterMenu={filterMenu}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => setShow(true)}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Member Name</TableCell>
              <TableCell>Account Name</TableCell>
              <TableCell>Billing Type</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body.map((item, index) => (
              <TableRow>
                <TableCell style={{ paddingLeft: '6px' }}>Maria Leonora D. Theresa</TableCell>
                <TableCell>Super Ferry</TableCell>
                <TableCell>Medical Collectible</TableCell>
                <TableCell align={'right'} style={{ paddingRight: '0' }}>
                  <Link to='/'>
                    <Typography variant='caption' className='text-link underline'>
                      Validate
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'approved-invoices-modal'}
        config={config}
        tile={'debitMemosSubmittedForApproval'}
        type='Approved Invoices'
        title='List of Approved Invoices'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        // fetchData={fetchData}
        params={params}
        accessors={['client.account_number', 'client.account_name', 'contract.type']}
      />
    </React.Fragment>
  )
}

MemberDebitMemosToValidateTile.displayName = 'DashboardTileComponent'

export default MemberDebitMemosToValidateTile
