import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
// TODO: Implement if Viewlist otherwise, remove
import ViewAll from '../../ViewAll3/ViewAll'

// Style
import classes from './ApprovedPreSOAs.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    {value: 'today', text: 'Today'},
    {value: 'week', text: 'This Week'},
    {value: 'month', text: 'This Month'}
  ],
  default: 'month'
}

// const columnConfig = [
//   { id: 'ticket-list-tile-table-header', name: '' },
//   { id: 'ticket-list-tile-table-header', name: 'ID' },
//   { id: 'ticket-list-tile-table-header', name: 'Due Date' },
//   { id: 'ticket-list-tile-table-header', name: 'Account Name' }
// ]

// Mock data
const mock = require('./../mock-data/collection-ticketList.json')

const ApprovedPreSOAs = props => {
  // Constants
  const { services = () => {} } = props
  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'name',
    order: true
  }

  const [hasCalled, setHasCalled] = React.useState(false)
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Approved Pre-SOAs',
    variant: 'h6'
  })
  // TODO: Implement if Viewlist otherwise, remove
  const [viewList, setViewList] = React.useState({
    count: 0,
    data: [],
    show: false,
    title: 'Tickets Waiting for Response'
  })

  // Initialize / Watch
  // React.useEffect(() => {
  //   const current_user_id = localStorage.getItem('user_id')
  //   services.getApprovedClientSOAs({ currentUserId: current_user_id }).then(res => {
  //     const data = res.data.data[0]
  //     setTile(i => ({ ...i, count: data.length, data }))
  //   })
  // }, [])

  const handleOnShowViewList = () => {
    setViewList({ ...initialValue, show: true, title: 'List of Client Transaction Journals to Setup' })
  }

  const fetchData = () => {
    if(!hasCalled){
      const current_user_id = localStorage.getItem('user_id')
      services.getApprovedClientSOAs({ currentUserId: current_user_id }).then(res => {
        const data = res.data.data[0]
        setTile(i => ({ ...i, count: data.length, data }))
      })
      setHasCalled(true)
    }
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props;
    const text = inViewport ? 'In viewport' : 'Not in viewport';
    return (
      <div className="viewport-block" ref={forwardedRef}></div>
    );
  };

  const ViewportBlock = handleViewport(Block)

  return (
    <React.Fragment>
      <ViewportBlock onEnterViewport={() => fetchData()} onLeaveViewport={() => console.log('Approved Pre-SOAs is not on viewport')} />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 3, value: tile.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{...tile.filterMenu}}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnShowViewList()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>

              <TableCell style={{paddingLeft: '6px'}}>Account Name</TableCell>
              <TableCell>Date Approved</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tile.data.map((item, index) => (
              
            <TableRow>
                <TableCell style={{padding: '0'}}>Star Mobile</TableCell>
                <TableCell>Dec 31, 2019</TableCell>
                <TableCell align={'right'} style={{paddingRight: '0'}}>
                    <Link to={`/billing/ledgers/${item.soa_number}/${'analyst'}`}>
                        <Typography variant='caption' className='text-link underline'>View</Typography>
                    </Link>
                </TableCell>
            </TableRow>
            ))}
            
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'client-transaction-ledgers-to-setup-modal'}
        config={config}
        tile={'approvedPreSoa'}
        type='Client Pre-SOAs To Analyze'
        title='List of Approved Pre SOAs'
        contents={tile.data}
        count={viewList.count}
        viewAll={
          window.location.hash.replace(/[#/]/g, '') === 'Client-Ledger-Setup' ? true : viewList
        }
        setViewAll={setViewList}
        linkCaption='Setup'
        passProperty='_id'
        modal='/#from_modal'
        subProperty='client'
        subSubProperty={'account_number'}
      />
    </React.Fragment>
  )
}

ApprovedPreSOAs.displayName = 'DashboardTileComponent'

export default ApprovedPreSOAs
