import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/cdf')

export default class CollectionCDFServices {
  static pregenerateCDF ({ data }) {
    return axios({
      url: `/collection-distribution/`,
      method: 'PUT',
      data
    })
  }

  static submit (data) {
    return axios({
      url: `/collection-distribution/client/submit`,
      method: 'POST',
      data
    })
  }

  static getAvailableSoa (params) {
    return axios({
      url: '/collection-distribution/soa/available',
      method: 'GET',
      params
    })
  }

  static getCDF (params) {
    return axios({
      url: `collection-distribution/${params}`,
      method: 'GET',
      params
    })
  }

  static getMany (params) {
    return axios({
      url: `/collection-distribution`,
      method: 'GET',
      params
    })
  }

  static update ({ data }) {
    return axios({
      url: `/collection-distribution/update/${data.cdfNumber}`,
      method: 'PUT',
      data
    })
  }

  static removeMember ({ data, params }) {
    return axios({
      url: `/collection-distribution/remove-member/${params}`,
      method: 'PATCH',
      data
    })
  }
}
