import React from 'react'
import { useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'
import noop from 'lodash/noop'
import pick from 'lodash/pick'

import Alert from 'SharedComponents/Alert/Alert'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import DatePicker from './../../Components/FormComponents/DatePicker/DatePicker'
import DetailTile from './../../Components/FormComponents/DetailTile/DetailTile'
import TextField from './../../Components/FormComponents/TextField/CustomTextField'
import PageFooter from './../../Components/PageFooter/PageFooter'
import FirstStepComponent from './components/SuggestForSuspension/SuggestForSuspension'
import SecondStepComponent from './components/SuspensionBreakDown/SuspensionBreakDown'

import CollectionSuspensionServices from 'Services/Api/CollectionSuspensionServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { SetSuspensionFields, UpdateSuspensionField } from './../../Store/actions'

import classes from './SuspensionPage.module.scss'

const mock = require('./mock-data/main-mock.json')
const soaMock = require('./mock-data/soa-data.json')

export default function SuspensionPage (props) {
  const dispatch = useDispatch()
  const history = useHistory()

  // TODO: Change labels and services based in data_type
  const { data_id = null, data_type = '' } = props.match.params
  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Analyze Details', 'Suspension Breakdown', 'Submit'],
    stepNumber: 1
  })
  const [pageTitle, setPageTitle] = React.useState('Suspension Page')
  const [willSubmit, setWillSubmit] = React.useState(false)
  const [willCancel, setWillCancel] = React.useState(false)
  const [confirmed, setConfirmed] = React.useState(false)

  React.useEffect(() => {
    dispatch(SetSuspensionFields({ ...mock, soa: soaMock }))
  }, [])

  const _data = useSelector(state => state.Suspension)

  // Methods
  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    switch (stepper.stepNumber) {
      case 1:
        return <FirstStepComponent {...componentProp} />
      case 2:
        return <SecondStepComponent {...componentProp} />
      case 3:
        return <h1>Step 3</h1>
      default:
        return ''
    }
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handlePageTitleChange = pageTitle => {
    setPageTitle(pageTitle)
  }

  const handleCancelSuspensionEndorsementConfirmed = () => {
    history.push('/')
  }

  const handleSubmitSuspensionEndorsementConfirmed = async () => {
    try {
      LoadingService.loading(true)

      await CollectionSuspensionServices.store({
        // accountNumber: _data.account_number,
        accountNumber: '7409-5462-2115', // TODO: Set account number from above.
        details: pick(_data, ['effectivity', 'reason', 'remarks']),
      })

      setWillSubmit(false)
      setConfirmed(true)

      LoadingService.loading(false)
    } catch (error) {
      setWillSubmit(false)
      // TODO: Show alert message containing the error.
      LoadingService.loading(false)
    }
  }

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header} direction='row' justify='space-between'>
            <Grid item>
              <Typography id='suspension-page-header' variant='h5'>
                {pageTitle}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <Grid container>
                <Grid item xs={12}>
                  <Grid container justify='space-between' spacing={2}>
                    <Grid key={'spn-no'} item>
                      <DetailTile id={'spn-no'} label={'spn no.'} text={_data.spn_number} />
                    </Grid>
                    <Grid key={'account-name'} item>
                      <DetailTile label={'account name'} text={_data.account_name} />
                    </Grid>
                    <Grid key={'account-no'} item>
                      <DetailTile label={'account no.'} text={_data.account_number} />
                    </Grid>
                    <Grid key={'account-tin'} item>
                      <DetailTile label={'account tin'} text={_data.account_tin} />
                    </Grid>
                    <Grid key={'broker'} item>
                      <DetailTile label={'broker'} text={_data.broker} />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12}>
                  <Grid container justify='space-between' spacing={2}>
                    <Grid key='effectivity-key' item>
                      <DatePicker
                        id='effectivity'
                        required
                        label='Effectivity of Suspension'
                        value={_data.effectivity}
                        onChange={event => {
                          dispatch(
                            UpdateSuspensionField({ field: 'effectivity', value: event.toISOString() })
                          )
                        }}
                        orientation={'column'}
                      />
                    </Grid>
                    <Grid key='reason-key' item xs>
                      <Grid item xs>
                        <TextField
                          id='reason'
                          label='Reason for Suspension'
                          value={_data.reason}
                          required
                          onChange={e => {
                            dispatch(
                              UpdateSuspensionField({
                                value: e.target.value,
                                field: 'reason'
                              })
                            )
                          }}
                        />
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    constainerStyle={{ width: 'unset', flex: 1 }}
                    multiline
                    rows={3}
                    id='remarks'
                    label='Remarks'
                    value={_data.remarks || ''}
                    onChange={event => {
                      dispatch(UpdateSuspensionField({ field: 'remarks', value: event.target.value }))
                    }}
                    required
                  />
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={12}>
              {/* {showComponent({ handlePageTitleChange })} */}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter
            isStepper
            showSuspensionListBtn
            stepNumber={stepper.stepNumber}
            stepCount={stepper.stepDetails.length}
            handleStepChange={handleStepChange}

            submitSuspensionEndorsement={willSubmit}
            handleSubmitSuspensionEndorsementConfirmed={handleSubmitSuspensionEndorsementConfirmed}
            handleSubmitSuspensionEndorsementCancelled={() => setWillSubmit(false)}
            handleSubmitSuspensionEndorsement={() => setWillSubmit(true)}

            cancelSuspensionEndorsement={willCancel}
            handleCancelSuspensionEndorsement={() => setWillCancel(true)}
            handleCancelSuspensionEndorsementConfirmed={handleCancelSuspensionEndorsementConfirmed}
            handleCancelSuspensionEndorsementCancelled={() => setWillCancel(false)}

            confirmSuspensionEndorsement={confirmed}
            handleConfirmSuspensionEndorsementConfirmed={() => setConfirmed(false)}
          />
        </Grid>
      </Grid>
      <Alert />
    </div>
  )
}
