import React from 'react'
import clsx from 'clsx'
import { Grid, Typography, Paper, IconButton } from '@material-ui/core'

// Components
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import DetailTile from './../../../../Components/FormComponents/DetailTile/DetailTile'
import SummaryTile from './../../../../Components/FormComponents/SummaryTile/SummaryTile'
import GroupOfCurrency from './../../../../Components/FormComponents/GroupOfCurrency/GroupOfCurrency'
import TextFieldCurrency from './../../../../Components/FormComponents/TextFieldCurrency/TextFieldCurrency'
import SubTotalTile from './../../../../Components/FormComponents/SubTotalTile/SubTotalTile'
import TextField from './../../../../Components/FormComponents/TextField/CustomTextField'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import { useSelector, useDispatch } from 'react-redux'
import { UpdateField } from './../../../../Store/actions'

// Icons
import KeyboardArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import AddIcon from '@material-ui/icons/Add'

// Styles
import { makeStyles } from '@material-ui/core/styles'

// Mock
const mockLabels = require('../../mock-data/group-of-labels.json')
const mockSOA = require('../../mock-data/soa-data.json')

const useStyles = makeStyles(theme => ({
  title: {
    fontSize: '16px',
    fontWeight: 'bold'
  },
  details: {
    fontSize: '14px'
  },
  containerPadding: {
    padding: '1rem'
  },
  width: {
    width: '25%'
  }
}))

const labelProps = {
  labels: mockLabels
}

const SIZE_PADDING = 4

const PenaltyBreakDownComponent = props => {
  // stored data
  const dispatch = useDispatch()
  const _data = useSelector(state => state.EndorsedAccountPenalty)

  const [activeSlider, setActiveSlider] = React.useState(0)

  // Mui-Theming
  const muiClasses = useStyles()

  // Dashboard -> Aging Summary -> Endorsed
  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems([
      'collectionDistributionDashboard',
      'listOfAccountsWithAgingSummary',
      `clientAgingSummary:${'STAR MUSIC'}`,
      'endorseAccountForPenalty'
    ])
  })

  // Actions
  const onClickBackSlider = steps => {
    setActiveSlider(steps)
  }

  const onClickNextSlider = steps => {
    setActiveSlider(steps)
  }

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'pn-no'} item>
            <DetailTile id={'pn-no'} label={'pn no.'} text={_data.penalty_number} />
          </Grid>
          <Grid key={'account-name'} item>
            <DetailTile label={'account name'} text={_data.account_name} />
          </Grid>
          <Grid key={'account-no'} item>
            <DetailTile label={'account no.'} text={_data.account_number} />
          </Grid>
          <Grid key={'account-tin'} item>
            <DetailTile label={'account tin'} text={_data.account_tin} />
          </Grid>
          <Grid key={'broker'} item>
            <DetailTile label={'broker'} text={_data.broker} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Typography variant='h5'>Penalty Reference Breakdown</Typography>
      </Grid>
      <Grid item={12} sm={12} md={12} lg={12}>
        <Paper elevation={3}>
          <Grid container justify='space-between' spacing={2}>
            <Grid key={'total-taxable'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile id={'total-taxable'} label={'total taxable amount'} text={'PHP 222,000.00'} />
            </Grid>
            <Grid key={'total-tax'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile id={'total-tax'} label={'total tax (vat) amount'} text={'PHP 26,640.00'} />
            </Grid>
            <Grid key={'total-amount'} item xs={12} sm={12} md={4} lg={4}>
              <SummaryTile id={'total-amount'} label={'total amount'} text={'PHP 248,640.00'} />
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12} style={{ marginTop: '1rem' }}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={12} md={3} lg={3}>
            <Typography className={clsx(muiClasses.title, 'color-4')}>{'SUMMARY'}</Typography>
            <Typography className={muiClasses.details}>{'Total Taxable Amount'}</Typography>
            <Typography className={muiClasses.details}>{'PHP 222,000.00'}</Typography>
          </Grid>
          <Grid item xs={12} sm={12} md={7} lg={7}>
            <Grid container spacing={2}>
              <FormSlider
                activeSlider={activeSlider}
                details={mockSOA}
                // removeData={}
              />
            </Grid>
          </Grid>
          <Grid item xs={12} sm={12} md={2} lg={2}>
            <Grid container>
              <Grid style={{ minHeight: '50px' }}>
                {(activeSlider && activeSlider) > 0 ? (
                  <IconButton onClick={() => onClickBackSlider(activeSlider - 1)}>
                    <KeyboardArrowLeftIcon />
                  </IconButton>
                ) : (
                  ''
                )}
              </Grid>
              <Grid style={{ minHeight: '50px' }}>
                <IconButton onClick={() => onClickNextSlider(activeSlider + 1)}>
                  <KeyboardArrowRightIcon />
                </IconButton>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12} style={{ marginTop: '1rem' }}>
        <GroupOfCurrency
          {...labelProps}
          // todo func
        />
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12} style={{ background: '#dadada', margin: '1rem 0rem' }}>
        <Grid container spacing={SIZE_PADDING} className={muiClasses.containerPadding}>
          <Grid item xs={12} lg={12}>
            <Typography variant='h5'>Penalty Breakdown (Adjustments)</Typography>
          </Grid>
          <Grid item className={muiClasses.width}>
            <TextFieldCurrency label={'Credit Memo'} />
          </Grid>
          <Grid item className={muiClasses.width}>
            <TextFieldCurrency label={'Credit Memo Tax'} />
          </Grid>
          <Grid item className={muiClasses.width}>
            <TextFieldCurrency label={'Deletion'} />
          </Grid>
          <Grid item className={muiClasses.width}>
            <TextFieldCurrency label={'Deletion Tax'} />
          </Grid>
        </Grid>
        <Grid container spacing={SIZE_PADDING} className={muiClasses.containerPadding}>
          <Grid item className={muiClasses.width}>
            <TextFieldCurrency label={'EWT'} />
          </Grid>
          <Grid item className={muiClasses.width} />
          <Grid item className={muiClasses.width}>
            <TextFieldCurrency label={'Commission'} />
          </Grid>
          <Grid item className={muiClasses.width}>
            <TextFieldCurrency label={'Commission Tax'} />
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={12}>
            <TextField
              constainerStyle={{ width: 'unset', flex: 1 }}
              multiline
              rows={3}
              id='remarks'
              label='Remarks'
              value={_data.remarks || ''}
              onChange={event => {
                dispatch(UpdateField({ field: 'remarks', value: event.target.value }))
              }}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={12}>
            <SubTotalTile
              showSubTotalHeader
              subTotalHeader={'Sub Total (Adjustments)'}
              subTotalLabel={'Sub Total Taxable Amount'}
              subTotalTaxLabel={'Sub Total Tax Amount'}
            />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}

export default PenaltyBreakDownComponent
