import Grid from '@material-ui/core/Grid'
import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import { Typography } from '@material-ui/core'

// Components
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import { CustomTable as Table } from './../../../../Components/CustomTable'
import AddSOASelect from './../../../../Components/SoaMenu'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import { useSelector, useDispatch } from 'react-redux'

// Styles
import classes from './SuggestForSuspension.module.scss'

// Utitiles
import moment from 'moment'

// Config
import { SuggestForSuspensionConfig } from './../../config/config'

// Mock
const listMock = require('./../../mock-data/list-mock.json')
const soaDataMock = require('../../mock-data/soa-data.json')

export default function SuggestForSuspension (props) {
  const { handlePageTitleChange = () => {} } = props
  const dispatch = useDispatch()
  const _state = useSelector(state => state.Suspension)

  const [list, setList] = React.useState({
    body: listMock,
    count: 20,
    filter: {},
    header: SuggestForSuspensionConfig,
    limit: 1,
    order: true,
    orderBy: 'hc',
    page: 1,
    sort: ''
  })

  React.useState(() => {
    handlePageTitleChange('Suggest For Suspension')
    BreadcrumbsService.setSelectedItems([
      'collectionDistributionDashboard',
      'listOfAccountsWithAgingSummary',
      `clientAgingSummary:${'STAR MARGARINE'}`,
      'suggestForSuspension'
    ])
  }, [])

  return (
    <Grid container className={classes.root} spacing={2}>
      <Grid item xs={12}>
        <Typography variant='h5'>Suspension Reference</Typography>
      </Grid>
      <Grid item xs={12}>
        <Grid container direction='row'>
          <FormSlider activeSlider={0} details={_state.soa} />
        </Grid>
      </Grid>
      <Grid item className={classes['add-soa']}>
        <AddSOASelect details={soaDataMock} />
      </Grid>
      <Grid item xs={12}>
        <Grid container direction='row'>
          <Grid item xs>
            <Table
              data={list}
              setData={setList}
              body={_data =>
                _data.map((item, index) => (
                  <TableRow key={`suggest-for-suspension-list-item-${index}`}>
                    <TableCell id={`suggest-for-suspension-item-hc-${index}`}>{index}</TableCell>
                    <TableCell id={`suggest-for-suspension-item-member-id-${index}`}>{item.member_id}</TableCell>
                    <TableCell id={`suggest-for-suspension-item-last-name-${index}`}>{item.last_name}</TableCell>
                    <TableCell id={`suggest-for-suspension-item-first-name-${index}`}>{item.first_name}</TableCell>
                    <TableCell id={`suggest-for-suspension-item-middle-name-${index}`}>{item.middle_name}</TableCell>
                    <TableCell id={`suggest-for-suspension-item-reference-date-${index}`}>
                      {moment(new Date(item.reference_date)).format('ll')}
                    </TableCell>
                    <TableCell id={`suggest-for-suspension-item-end-date-${index}`}>
                      {moment(new Date(item.end_date)).format('ll')}
                    </TableCell>
                    <TableCell id={`suggest-for-suspension-item-plan-type-${index}`}>{item.plan_type}</TableCell>
                  </TableRow>
                ))
              }
            />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}
