import { combineReducers } from 'redux'
import CollectionDistribution from './CollectionDistributionReducer'
import Suspension from './SuspensionReducer'
import EndorsedAccountPenalty from './EndorsedAccountPenaltyReducer'
import MeansOfPayment from './MeansOfPaymentReducer'

const reducers = combineReducers({
  CollectionDistribution,
  Suspension,
  EndorsedAccountPenalty,
  MeansOfPayment
})

export default reducers
