import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll3/ViewAll'

// Style
import classes from './PreSOAsSubmittedForApprovalTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(1, 'weeks').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(1, 'months').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const PreSOAsSubmittedForApprovalTile = props => {
  // Constants
  const { service = () => {} } = props

    const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: 'name',
    page: 1,
    sort: ''
  }
  
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})
  const [isFetching, setIsFetching] = React.useState(false)

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month']});
      setHasCalled(true)
    }
  }

  // const fetchData = () => {
  //   console.log(hasCalled)
  //   const current_user_id = localStorage.getItem('user_id')
  //   BillingAnalystServices.getSubmittedClientSOAs({ currentUserId: current_user_id }).then(res => {
  //     let data = null
  //     try {
  //       data = res.data.data[0]
  //     } catch (error) {
  //       data = []
  //     }
  //     // console.log(data)
  //     setTile(i => ({ ...i, count: data.length, data }))
  //   })
  //   setHasCalled(true)
  // }

  const fetchData = params => {
    setIsFetching(true)
    const current_user_id = localStorage.getItem('user_id')
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      currentUserId: current_user_id,
      ...params
    }
    setParams(_params)

    BillingAnalystServices.getSubmittedClientSOAs(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setList(i => ({ ...i, body: results, count, limit, page }))
      setIsFetching(false)
    })
  }

  const fetchTileData = (params) => {
    const current_user_id = localStorage.getItem('user_id')
    const _params = {
      currentUserId: current_user_id,
      ...params
    }
    BillingAnalystServices.getSubmittedClientSOAsTile(_params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value]})
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props;
    const text = inViewport ? 'In viewport' : 'Not in viewport';
    return (
      <div className="viewport-block" ref={forwardedRef}></div>
    );
  };

  const ViewportBlock = handleViewport(Block)

  return (
    <React.Fragment>
      <ViewportBlock onEnterViewport={() => viewPortGetData()} onLeaveViewport={() => console.log('Pre-SOAs Submitted for Approval Tile is not on viewport')} />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 3, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Pre-SOAs Submitted for Approval'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => { setShow(true); fetchData({}) }}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{paddingLeft: '6px'}}>Account Name</TableCell>
              <TableCell>Billing Cycle</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body && tileList.body.map((item, index) => (
              <TableRow key={index}>
                <TableCell style={{padding: '0', maxWidth: '50px'}}>{item.account_name}</TableCell>
                <TableCell>{item.billing_cycle}</TableCell>
                <TableCell align={'right'} style={{paddingRight: '0'}}>
                  <Link to={`/billing/ledgers/${item.soa_number}/${'analyst'}`}>
                    <Typography variant='caption' className='text-link underline'>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'client-transaction-ledgers-to-setup-modal'}
        config={config}
        tile={'preSoaSubmittedForApproval'}
        type='Pre-SOAs Submitted for Approval'
        title='List of Pre-SOAs Submitted for Approval'
        placeholder='Search List of Pre-SOAs Submitted for Approval'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['account_number', 'account_name', 'billing_cycle', 'billing_cycle_code']}
      />
    </React.Fragment>
  )
}

PreSOAsSubmittedForApprovalTile.displayName = 'DashboardTileComponent'

export default PreSOAsSubmittedForApprovalTile
