const ActionTabs = props => {
  const { headCells, users, clientInfo, onShowViewMore, viewMore, setViewMore, pagination, params, fetchData, changeView } = props
  return (
    // TODO permissions (if necessary) and Tab Components
    <CustomTabs defaultIndex={0}>
      <CustomTab id='billing-analyst-tab' label='BILLING ANALYST'>
          <BillingAnalystTab
            headCells={headCells}
            users={users}
            onShowViewMore={onShowViewMore}
            viewMore={viewMore}
            setViewMore={setViewMore}
            clientInfo={clientInfo}
            pagination={pagination}
            params={params}
            fetchData={fetchData}
            changeView={changeView}
          />
      </CustomTab>
      {/* <CustomTab id='billing-cycle-tab' label='BILLING CYCLE'>
        <BillingCycleTab />
      </CustomTab> */}
      <CustomTab id='billing-instructions-tab' label='BILLING INSTRUCTIONS'>
        <BillingInstructionsTab />
      </CustomTab>
    </CustomTabs>
  )
}

