import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'
import ViewAll from '../../ViewAll3/ViewAll'
// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
// TODO: Implement if Viewlist otherwise, remove
import ViewList from './../../ViewList2/ViewList'

// Style
import classes from './PendingPreSOAsTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const PendingPreSOAsTile = props => {
  // Constants
  const { services = () => {} } = props

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'name',
    order: true
  }

  const [hasCalled, setHasCalled] = React.useState(false)
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Pending Pre-SOAs',
    variant: 'h6'
  })

  const [viewList, setViewList] = React.useState({
    count: 0,
    data: [],
    show: false,
    title: 'Tickets Waiting for Response'
  })

  // Initialize / Watch
  // React.useEffect(() => {
  //   const current_user_id = localStorage.getItem('user_id')
  //   services.getSavedClientSOAs({ currentUserId: current_user_id }).then(res => {
  //     const data = res.data.data[0]
  //     console.log(data)
  //     setTile(i => ({...i, count: data.length, data}))
  //   })
  // }, [])

  const handleOnShowViewList = () => {
    setViewList({ ...initialValue, show: true, title: 'List of Client Transaction Journals to Setup' })
  }

  const fetchData = () => {
    if (!hasCalled) {
      console.log('Pending Pre-SOAs Tile is on viewport')
      const current_user_id = localStorage.getItem('user_id')
      services.getSavedClientSOAs({ currentUserId: current_user_id }).then(res => {
        const data = res.data.data[0]
        console.log(data)
        setTile(i => ({ ...i, count: data.length, data }))
      })
      setHasCalled(true)
    }
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)

  const assignTicket = soaNumber => {
    services.assignClientSOA({ soaNumber: soaNumber })
  }

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => fetchData()}
        onLeaveViewport={() => console.log('Pending Pre-SOAs Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 3, value: tile.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnShowViewList()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Account Name</TableCell>
              <TableCell>Billing Cycle</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tile.data.map((item, index) => (
              <TableRow>
                <TableCell style={{ padding: '0' }}>{item.account_name}</TableCell>
                <TableCell>{item.billing_cycle}</TableCell>
                <TableCell align={'right'} style={{ paddingRight: '0' }}>
                  <Link
                    to={`/billing/ledgers/${item.soa_number}/${'analyst'}`}
                    onClick={() => assignTicket(item.soa_number)}
                  >
                    <Typography variant='caption' className='text-link underline'>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'pending-pre-soas-modal'}
        config={config}
        tile={'pendingPreSoas'}
        type='Pending Pre-Soas'
        title='List of Pending Pre-Soas'
        contents={tile.data}
        viewAll={viewList}
        setViewAll={setViewList}
        count={viewList.count}
        linkCaption='View'
        passProperty={'soa_number'}
        route='/billing/ledgers/'
      />
    </React.Fragment>
  )
}

PendingPreSOAsTile.displayName = 'DashboardTileComponent'

export default PendingPreSOAsTile
