import instance from './instance'
const axios = instance('/b/ccm')

export default class BillingClientCreditMemosServices {
  static getCreditMemoDetails ({ transaction_number }) {
    return axios({
      url: `/billing/credit-memo-details/${transaction_number}`,
      method: 'GET'
    })
  }

  static getTobeRevisedCreditMemo ({ transaction_number }) {
    return axios({
      url: `/billing/credit-get/${transaction_number}`,
      method: 'GET'
    })
  }

  static getCreditMemosNew (params) {
    return axios({
      url: `/billing/credit-new`,
      method: 'GET',
      params
    })
  }

  static getCreditMemosNewTile (params) {
    return axios({
      url: `/billing/credit-new-tile`,
      method: 'GET',
      params
    })
  }
  static getCreditMemosSaved () {
    return axios({
      url: `/billing/credit-save`,
      method: 'GET'
    })
  }
  static getCreditMemosForApproval (params) {
    return axios({
      url: `/billing/credit-for-approval`,
      method: 'GET',
      params
    })
  }
  static getCreditMemosForApprovalTile (params) {
    return axios({
      url: `/billing/credit-for-approval-tile`,
      method: 'GET',
      params
    })
  }
  static getCreditMemosForApprovalAnalyst (params) {
    return axios({
      url: `/billing/credit-for-approval-analyst`,
      method: 'GET',
      params
    })
  }
  static getCreditMemosForApprovalAnalystTile (params) {
    return axios({
      url: `/billing/credit-for-approval-analyst-tile`,
      method: 'GET',
      params
    })
  }
  static getCreditMemosValidated (params) {
    return axios({
      url: `/billing/credit-validated`,
      method: 'GET',
      params
    })
  }
  static getCreditMemosApproved () {
    return axios({
      url: `/billing/credit-approved`,
      method: 'GET'
    })
  }

  static createCreditMemo ({data}) {
    return axios({
      url: `/billing/credit-create`,
      method: 'POST',
      data
    })
  }

  static assignAnalyst ({ transaction_number }) {
    return axios({
      url: `/billing/credit/credit-assign/${transaction_number}`,
      method: 'PUT'
    })
  }

  static assignSupervisor ({ transaction_number }) {
    return axios({
      url: `/billing/credit/credit-assign-supervisor/${transaction_number}`,
      method: 'PUT'
    })
  }
  static routeCreditMemo ({ transaction_number, action }) {
    return axios({
      url: `/billing/credit/route-ticket?action=${action}&transactionNumber=${transaction_number}`,
      method: 'PUT'
    })
  }
}
