// React
import React from 'react'
import { useHistory } from 'react-router-dom'

// Material
import Button from '@material-ui/core/Button'
import { Checkbox, Grid, TableCell, TableRow, Typography } from '@material-ui/core'

// Components
import ControlList from './../../../Components/FormComponents/ControlList/ControlList'
import { CustomTable as Table } from 'Pages/Protected/BillingAndCollection/Components/CustomTable'
import SearchTextComponent from 'Pages/Protected/BillingAndCollection/Components/FormComponents/SearchTextComponent/SearchTextComponent'
import SOAModal from './SOAModal'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionClientLedgerServices from 'Services/Api/CollectionClientLedgerServices'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import moment from 'moment'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Config
import header from './config/config'
import Totals from './Totals'


const MemberAgingSummaryPage = props => {
  const { accountNumber } = props.match.params
  const history = useHistory()
  const [open, setOpen] = React.useState(false)
  const [selectedSoa, setSelectedSoa] = React.useState({})
  const [soa, setSoa] = React.useState(null)
  const [state, setState] = React.useState({
    body: [],
    count: 20,
    filter: {},
    header: header,
    limit: 1,
    order: true,
    orderBy: 'soa_date',
    page: 1,
    sort: ''
  })

  React.useEffect((accountNumber) => {
    LoadingService.loading(true)
    Promise.all([CollectionClientLedgerServices.getClientDetails({ accountNumber })]).then(res => {
      const [data] = res
      const { account_name, soa } = data.data.data

      PageTitleService(`Julius Narag- Aging Summary`)
      BreadcrumbsService.setSelectedItems(['listOfMembersWithAgingSummary', `memberAgingSummary:JULIUS NARAG`])
      setState(i => ({ ...i, body: soa.results, count: soa.count }))
    }).catch(error => {
      doMock()
    })
    LoadingService.loading(false)
  }, [accountNumber])

  // TODO: Remove - temporary mock data for api errors
  const doMock = () => {
    const mock = require('./mock-data/mock.json')
    const { account_name, soa: { results, count } } = { account_name: 'STAR MUSIC', soa: { results: mock, count: 10 } }
    PageTitleService(`Julius Narag- Aging Summary`)
    BreadcrumbsService.setSelectedItems(['listOfMembersWithAgingSummary', `memberAgingSummary:JULIUS NARAG`])
    setState(state => ({ ...state, body: results, count }))
  }

  // Methods
  const displaySettingsHandler = () => {
    console.log('display settings clicked')
  }

  const handleClose = () => {
    setOpen(false)
  }
  const handleClickOpen = soa_no => {
    setSoa(soa_no)
    setOpen(true)
  }

  const handlePregenerateCDF = () => {
    LoadingService.loading(true)
    const _soa = []
    Object.keys(selectedSoa).map(i => {
      if (selectedSoa[i]) {
        _soa.push(i)
      }
    })
    // _account_number
    CollectionCDFServices.pregenerateCDF({ data: { accountNumber, soa: _soa } })
      .then(res => {
        LoadingService.loading(false)
        const { cdfNumber } = res.data.data
        history.push(`/collection/collection-distribution/client/${cdfNumber}`)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(err)
      })
  }

  const onCheckboxChanged = params => {
    const { soa_number, value } = params
    setSelectedSoa(i => ({ ...i, [soa_number]: value }))
  }

  const renderBody = _data =>
    _data.map(({ due_date, total_due, soa_date, soa_no, i }) => (
      <TableRow key={`aging-summary-table-row-${soa_no}`}>
        <TableCell id={`account-no-${i}`}>
          <Checkbox
            onChange={e => {
              onCheckboxChanged({ soa_no, value: e.target.checked })
            }}
            inputProps={{ 'aria-label': 'primary checkbox' }}
          />
        </TableCell>
        <TableCell id={`soa-date-${i}`}>{moment(soa_date).format('ll')}</TableCell>
        <TableCell id={`soa-no-${i}`}>
          <Button onClick={() => handleClickOpen(soa_no)}>{soa_no}</Button>
        </TableCell>
        <TableCell id={`due-date-${i}`}>{moment(due_date).format('ll')}</TableCell>
        <TableCell id={`days-overdue-${i}`}>{0}</TableCell>
        <TableCell id={`total-amount-${i}`}>{total_due}</TableCell>
        <TableCell id={`account-no-${i}`}>
          <Typography
            id={`account-no-${i}`}
            variant='caption'
            className={`text-link underline`}
            onClick={i => {
              console.log('View clicked:' + i)
            }}
          >
            Create CD
          </Typography>
        </TableCell>
      </TableRow>
    ))

  const searchChangeHandler = _data => {
    console.log('search change handler: ' + _data)
  }

  const searchProps = {
    placeholder: 'Search Julius Narag - Aging Summary',
    displaySettings: true,
    handleOnDisplaySettingsClicked: displaySettingsHandler,
    onChange: searchChangeHandler,
    exportFiles: true
  }

  const tableProps = {
    showCheckBox: true,
    data: state,
    setData: setState,
    body: renderBody,
    hasLink: true
  }

  return (
    <React.Fragment>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <SearchTextComponent {...searchProps} />
        </Grid>
        <Grid item xs={12}>
          <Totals />
        </Grid>
        <Grid item xs={12}>
          <ControlList
            createCollectionDistribution
            endorseForPenalty
            suggestForSuspension
            handleOnCreateCollectionDistributionClicked={() => handlePregenerateCDF()}
          />
        </Grid>
        <Grid item xs={12}>
          <Table {...tableProps} />
        </Grid>
      </Grid>
      <SOAModal {...{ open, handleClose, soa }} />
    </React.Fragment>
  )
}

export default MemberAgingSummaryPage
