import React from 'react'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'

// Components
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import FirstStepComponent from './components/AnalyzeDetailsComponent/AnalyzeDetailsComponent'
import SecondStepComponent from './components/PenaltyBreakDownComponent/PenaltyBreakDownComponent'
import ThirdStepComponent from './components/PenaltyDetailsComponent/PenaltyDetailsComponent.js'

// import FourthStepComponent from './components/SubmitModal/SubmitModal'
import PageFooter from './../../Components/PageFooter/PageFooter'

// Store
import { useDispatch } from 'react-redux'
import { SetEndorsedPenalty } from './../../Store/actions'

// Styles
import classes from './CollectionEndorsedAccountPenaltyPage.module.scss'

// Mock-data
const mock = require('./mock-data/account-penalty.json')

// Soa
const mockForSOA = require('./mock-data/soa-data.json')

export default function CollectionEndorsedAccountPenaltyPage (props) {
  const dispatch = useDispatch()
  // TODO: Change labels and services based in data_type
  const { data_id = null, data_type = '' } = props.match.params
  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Analyze Details', 'Penalty Breakdown', 'Penalty Details', 'Submit'],
    stepNumber: 1
  })

  const [pageTitle, setPageTitle] = React.useState('Endorse Account for Penalty')

  React.useEffect(() => {
    dispatch(SetEndorsedPenalty({ ...mock, soa: mockForSOA }))
  }, [])

  // Methods
  const showComponent = componentProp => {
    // TODO
    switch (stepper.stepNumber) {
      case 1:
        return <FirstStepComponent {...componentProp} />
      case 2:
        return <SecondStepComponent {...componentProp} />
      case 3:
        return <ThirdStepComponent {...componentProp} />
      case 4:
        return <h4>Step 4</h4>
      default:
        return ''
    }
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handlePageTitleChange = pageTitle => {
    setPageTitle(pageTitle)
  }

  const pageProps = {
    handleStepChange: handleStepChange,
    stepNumber: stepper.stepNumber,
    stepCount: stepper.stepDetails.length,
    showEndorsedAccountPenaltyBtn: true
  }

  return (
    <div className={classes.root}>
      <Grid
        container
        className={`${classes['grid-container']} ${classes['bg-white']} ${classes.header}`}
        spacing={2}
        direction='column'
        justify='space-between'
      >
        <Grid item xs={12}>
          <Grid container>
            <Grid item className={classes['grid-item']}>
              <Typography id='collection-distribution-page-header' variant='h5'>
                {pageTitle}
              </Typography>
            </Grid>
            <Grid item className={classes['grid-item']}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              {showComponent({ handlePageTitleChange })}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter {...pageProps} />
        </Grid>
      </Grid>
    </div>
  )
}
