import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableBody from '@material-ui/core/TableBody'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Checkbox from '@material-ui/core/Checkbox'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'
import Typography from '@material-ui/core/Typography'
import ViewListIcon from '@material-ui/icons/ViewList'
import Link from '@material-ui/core/Link'
import * as moment from 'moment'

import ContractType from 'Constant/ContractTypes'
import FieldCode from 'Constant/MemberTableFields'

// Components
import Table from 'SharedComponents/Table/Table'
import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
import EnhancedTable from 'StandardComponents/EnhancedTable/EnhancedTable'

import classes from './Analyze.module.scss'
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

import BillingSOAServices from 'Services/Api/BillingSOAServices'

export default function Analyze (props) {
  const { values = {} } = props
  const [soaDetails, setSOADetails] = React.useState(null)
  const [selected, setSelected] = React.useState([])
  const [headCells, setHeadCells] = React.useState([
    { id: 'remove-icon', numeric: false, center: true, disablePadding: false, label: '' },
    { id: 'head-count', numeric: false, center: true, disablePadding: false, label: 'HC' },
    { id: 'account-number', numeric: false, center: true, disablePadding: true, label: 'ACCOUNT NUMBER' },
    { id: 'last-name', numeric: false, center: true, disablePadding: true, label: 'LAST NAME' },
    { id: 'first-name', numeric: false, center: true, disablePadding: true, label: 'FIRST NAME' },
    { id: 'middle-initial', numeric: false, center: true, disablePadding: true, label: 'MIDDLE INITIAL' },
    { id: 'reference-date', numeric: false, center: true, disablePadding: true, label: 'REFERENCE DATE' },
    { id: 'renewal-date', numeric: false, center: true, disablePadding: true, label: 'RENEWAL DATE' },
    { id: 'end-date', numeric: false, center: true, disablePadding: true, label: 'END DATE' },
    { id: 'plan-type', numeric: false, center: true, disablePadding: true, label: 'PLAN TYPE' },
    { id: 'membership-fees', numeric: true, disablePadding: false, label: 'MEMBERSHIP FEES' },
    { id: 'processing-fees', numeric: true, disablePadding: false, label: 'PROCESSING FEES' },
    { id: 'total', numeric: true, disablePadding: false, label: 'TOTAL' },
    { id: 'remarks', numeric: false, center: true, disablePadding: true, label: 'REMARKS' }
  ])
  const [riderColumns, setRiderColumns] = React.useState([])
  const [riderFees, setRiderFees] = React.useState([])
  const [membershipFees, setMembershipFees] = React.useState([])
  const [processingFees, setProcessingFees] = React.useState([])

  React.useEffect(() => {
    if (values.soa) {
      setSOADetails(values.soa)

      BillingSOAServices.getRiders({ journalId: values.soa.journal._id }).then(value => {
        setRiderColumns(value.data.data)
      })

      BillingSOAServices.getRiderFees({ journalId: values.soa.journal._id }).then(value => {
        setRiderFees(value.data.data)
      })

      BillingSOAServices.getMembershipFees({ journalId: values.soa.journal._id }).then(value => {
        setMembershipFees(value.data.data)
      })

      BillingSOAServices.getProcessingFees({ journalId: values.soa.journal._id }).then(value => {
        setProcessingFees(value.data.data)
      })
    }
  }, [values.soa])

  React.useEffect(() => {
    if (riderColumns) {
      riderColumns.map(r => {
        let rider = {
          id: r.replace(/[^A-Z0-9]/gi, '-').toLowerCase(),
          numeric: false,
          disablePadding: true,
          label: r.toUpperCase()
        }
        let tempHeadCells = headCells
        tempHeadCells.splice(-2, 0, rider)
        setHeadCells(tempHeadCells)
      })
    }
  }, [riderColumns])

  return (
    <div className={classes.root}>
      {values.type !== 'validated' && soaDetails && (
        <ContentHeader
          summaryFields={['Sub Total', 'Tax Amount', 'Total Amount']}
          summaryValues={
            soaDetails.journal && soaDetails.journal.summary
              ? [
                  soaDetails.journal.summary.sub_total,
                  soaDetails.journal.summary.tax_amount,
                  soaDetails.journal.summary.sub_total + soaDetails.journal.summary.tax_amount
                ]
              : [0, 0, 0]
          }
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-soa-number' color='textSecondary'>
                SOA NO.
              </Typography>
              <Typography id='content-details-billing-coverage-field' color='textSecondary'>
                Period Covered
              </Typography>
              <Typography id='content-details-due-date-field' color='textSecondary'>
                Due Date
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-soa-number-value'>{soaDetails.soa_number}</Typography>
              <Typography id='content-details-billing-coverage-value' color='textSecondary'>
                {soaDetails.period &&
                  DateFormatterByAbbreviation(soaDetails.period.start) +
                    ' - ' +
                    DateFormatterByAbbreviation(soaDetails.period.end)}
              </Typography>
              <Typography id='content-details-due-date-value' color='textSecondary'>
                {DateFormatterByAbbreviation(soaDetails.due_date)}
              </Typography>
            </div>
            <Typography id='content-details-soa-staus' color='error'>
              {soaDetails.journal && soaDetails.journal.soa && soaDetails.journal.soa.status}
            </Typography>
          </div>
        </ContentHeader>
      )}
      {soaDetails ? (
        <EnhancedTable
          headCells={headCells}
          // handleSelectAllClick={() => handleSelectAllClick({ members: soaDetails.approved_invoices })}
          // indeterminate={!!soaDetails.approved_invoices.indeterminate}
          // checked={!!soaDetails.approved_invoices.checked}
          checkAll
          checkboxColor='default'
        >
          <TableBody>
            {soaDetails.journal &&
              soaDetails.journal.member.map((item, index) => {
                return (
                  <TableRow>
                    <TableCell padding='checkbox'>
                      <Checkbox color='default' />
                    </TableCell>
                    <TableCell>
                      <RemoveCircleIcon id={`remove-${index}`} className={classes.removeIcon} />
                    </TableCell>
                    <TableCell id={`head-count-${index}`} align='right'>
                      {index + 1}
                    </TableCell>
                    <TableCell id={`account-number-${index}`}>{item.account_no}</TableCell>
                    <TableCell id={`last-name-${index}`}>{item.last_name}</TableCell>
                    <TableCell id={`first-name-${index}`}>{item.first_name}</TableCell>
                    <TableCell id={`middle-initial-${index}`}>{item.middle_name}</TableCell>
                    <TableCell id={`reference-date-${index}`}>
                      {moment(item.effectivity_date).format('MMM.DD,YYYY')}
                    </TableCell>
                    <TableCell id={`renewal-date-${index}`}>
                      {moment(item.renewal_date).format('MMM.DD,YYYY')}
                    </TableCell>
                    <TableCell id={`end-date-${index}`}>{moment(item.end_date).format('MMM.DD,YYYY')}</TableCell>
                    <TableCell id={`plan-type-${index}`}>{item.plan_type_name}</TableCell>
                    <TableCell id={`membership-fee-${index}`} align='right'>
                      {membershipFees.map((m, i) => {
                        if (item._id === m.memberId) return CurrencyFormatter(m.fees)
                      })}
                    </TableCell>
                    <TableCell id={`processing-fee-${index}`} align='right'>
                      {processingFees.map((m, i) => {
                        if (item._id === m.memberId) return CurrencyFormatter(m.fees)
                      })}
                    </TableCell>
                    {riderColumns &&
                      riderColumns.map((rc, i) => {
                        if (riderFees && riderFees.findIndex(rf => rf.type === rc && rf.memberId === item._id) > -1) {
                          return (
                            <TableCell>
                              {CurrencyFormatter(
                                riderFees[riderFees.findIndex(rf => rf.type === rc && rf.memberId === item._id)].fee
                              )}
                            </TableCell>
                          )
                        } else return <TableCell>{CurrencyFormatter(0)}</TableCell>
                      })}
                    <TableCell id={`total-${index}`} align='right'>
                      {CurrencyFormatter(item.total_fees)}
                    </TableCell>
                    <TableCell id={`remarks-${index}`}>remarks</TableCell>
                  </TableRow>
                )
              })}
          </TableBody>
        </EnhancedTable>
      ) : (
        'No Data'
      )}
    </div>
  )
}
