import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll2/ViewAll'

// Style
import classes from './InvoicesSubmittedForApprovalTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  today: {
    start: moment()
      .startOf('day')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  },
  week: {
    start: moment()
      .startOf('day')
      .subtract(1, 'weeks')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  },
  month: {
    start: moment()
      .subtract(1, 'months')
      .startOf('day')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  }
}

const InvoicesSubmittedForApprovalTile = props => {
  // Constants
  const { services = () => {} } = props

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchData({})
      fetchTileData({ range: rangeDate['month'] })
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      ...params
    }
    setParams(_params)

    services.getInvoicesForApprovalAnalyst(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setList(i => ({ ...i, body: results, count, limit, page }))
    })
  }

  const fetchTileData = params => {
    services.getInvoicesForApprovalAnalystTile(params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = value => {
    fetchTileData({ range: rangeDate[value] })
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Invoices Submitted for Approval is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Invoices Submitted for Approval'}
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => setShow(true)}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Transaction No.</TableCell>
              <TableCell>Submitted Date</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body &&
            tileList.body.map((item, index) => (
              <TableRow>
                <TableCell style={{ paddingLeft: '6px' }}>{item.transaction.number}</TableCell>
                <TableCell>{moment(item.date.modified).format('MM/DD/YYYY')}</TableCell>
                <TableCell align={'right'} style={{ paddingRight: '0' }}>
                  <Link to={`/billing/invoice/forapproval/${item.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'approved-invoices-modal'}
        config={config}
        tile={'invoicesSubmittedForApproval'}
        type='Invoice Submitted'
        title='List of Invoices Submitted for Approval'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        accessors={['transaction.number', 'billing_client_contract.client.account_name']}
      />
    </React.Fragment>
  )
}

InvoicesSubmittedForApprovalTile.displayName = 'DashboardTileComponent'

export default InvoicesSubmittedForApprovalTile
