import React from 'react'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'

// SUPERVISOR
// import FirstStepComponent from './components/AnalyzeDetailsComponent/AnalyzeDetailsComponent'
import FirstStepComponent from './components/CollectionBreakDownComponent/CollectionBreakDownComponent'
import SecondStepComponent from './components/CollectionDetailsComponent/CollectionDetailsComponent'

// Components
import PageFooter from '../../Components/PageFooter/PageFooter'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'

// Store
import { useDispatch } from 'react-redux'
import { SetFields } from './../../Store/actions'

// Styles
import classes from './CollectionDistributionToValidatePage.module.scss'

// Mock-data
const mock = require('./mock-data/collection-distribution.json')

export default function CollectionDistributionToValidatePage (props) {
  // send data in redux store
  const dispatch = useDispatch()

  // TODO: Change labels and services based in data_type
  const { data_id = null, data_type = '' } = props.match.params

  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Collection Breakdown', 'Collection Details', 'Approve'],
    stepNumber: 1
  })

  const [pageTitle, setPageTitle] = React.useState('Collection Distribution to Validate')

  React.useEffect(() => {
    dispatch(SetFields({ ...mock }))
  }, [])

  // Methods
  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    if ((data_type && data_type) === 'client') {
      switch (stepper.stepNumber) {
        case 1:
          return <FirstStepComponent {...componentProp} dataType={data_type} />
        case 2:
          return <SecondStepComponent {...componentProp} />
        default:
          return ''
      }
    } else if ((data_type && data_type) === 'member') {
      switch (stepper.stepNumber) {
        case 1:
          return <FirstStepComponent {...componentProp} />
        case 2:
          return <SecondStepComponent {...componentProp} />
        default:
          return ''
      }
    }
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handlePageTitleChange = pageTitle => {
    setPageTitle(pageTitle)
  }

  const pageProps = {
    isStepper: true,
    handleStepChange: handleStepChange,
    stepNumber: stepper.stepNumber,
    stepCount: stepper.stepDetails.length,
    showCDToValidateBtn: true
  }

  return (
    <div className={classes.root}>
      <Grid
        container
        className={`${classes['grid-container']} ${classes['bg-white']} ${classes.header}`}
        spacing={2}
        direction='column'
        justify='space-between'
      >
        <Grid item xs={12}>
          <Grid container>
            <Grid item className={classes['grid-item']}>
              <Typography id='collection-distribution-page-header' variant='h5'>
                {pageTitle}
              </Typography>
            </Grid>
            <Grid item className={classes['grid-item']}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              {showComponent({ handlePageTitleChange })}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter {...pageProps} />
        </Grid>
      </Grid>
    </div>
  )
}
