import React from 'react'
import { Switch, Route } from 'react-router-dom'

// Routes by role
import BillingModuleRoutes from './Routes'

// RBAC
import RBAC from 'Security/RBAC'

export default function BillingModule () {
  const [isRbacLoaded, setIsRbacLoaded] = React.useState(false)

  const loadRbac = async () => {
    await RBAC.load()
    setIsRbacLoaded(true)
  }
  React.useEffect(() => {
    document.title = 'HIMS - Billing'
    loadRbac()

    return () => RBAC.unload()
  }, [])

  return <React.Fragment>
    {isRbacLoaded && <Switch>
      <Route path='/' component={BillingModuleRoutes} />
    </Switch> }
  </React.Fragment>
}
