import React from 'react';
import PropTypes from 'prop-types';
import { Editor, Transforms, createEditor } from 'slate';
import { withHistory } from 'slate-history';
import { Slate, Editable, withReact, useSlate, ReactEditor } from 'slate-react';
import isHotkey from 'is-hotkey';
import noop from 'lodash/noop';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import grey from '@material-ui/core/colors/grey';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';
import EditIcon from '@material-ui/icons/Edit';
import RemoveRedEyeIcon from '@material-ui/icons/RemoveRedEye';
import FormatBoldIcon from '@material-ui/icons/FormatBold';
import FormatItalicIcon from '@material-ui/icons/FormatItalic';
import FormatUnderlinedIcon from '@material-ui/icons/FormatUnderlined';
import LooksOneIcon from '@material-ui/icons/LooksOne';
import LooksTwoIcon from '@material-ui/icons/LooksTwo';
import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted';
import FormatListNumberedIcon from '@material-ui/icons/FormatListNumbered';
import BorderAllIcon from '@material-ui/icons/BorderAll';
import InsertPhotoIcon from '@material-ui/icons/InsertPhoto';
import AddIcon from '@material-ui/icons/Add';

import Button from './Button';
import Element from './Element';
import Leaf from './Leaf';

import { HOTKEYS, LIST_TYPES, DEFAULT_VALUE } from './constants';

const useStyles = makeStyles(theme => ({
  label: {
    paddingBottom: 5
  },

  canvas: {
    boxShadow: '0.5px 0.5px 1px 0.8px rgba(0, 0, 0, 0.4)',
  },

  canvasFocused: {
    borderWidth: 1,
    borderStyle: 'solid',
    borderColor: 'green'
  },

  toolbar: {
    padding: 10,
    borderBottomWidth: 1,
    borderBottomStyle: 'solid',
    borderBottomColor: theme.palette.grey[200]
  },

  wrapperPreview: {
    padding: theme.spacing(8),
    backgroundColor: theme.palette.grey[800]
  },

  body: {
    cursor: 'text',
    minHeight: theme.spacing(40),
    padding: 12,
    color: 'rgba(0, 0, 0, 0.87)',
    fontSize: '1rem',
    fontWeight: 400,
    lineHeight: '1.1875em',
    letterSpacing: '0.00938em',
  },

  bodyPreview: {
    padding: theme.spacing(12),
    background: theme.palette.common.white
  }
}));

export default function XEditor({ className, id, label = null, defaultValue = '', onChange = noop }) {
  const classes = useStyles();

  const [value, setValue] = React.useState(JSON.parse(defaultValue || DEFAULT_VALUE));
  const [focused, setFocused] = React.useState(false)
  const [preview, setPreview] = React.useState(false)

  const renderElement = React.useCallback(props => <Element {...props} />, []);
  const renderLeaf = React.useCallback(props => <Leaf {...props} />, []);
  const editor = React.useMemo(() => withHistory(withReact(createEditor())), []);

  function handleToggleClicked() {
    setPreview(!preview)
  }

  function handleFocus(event) {
    setFocused(true)
  }

  function handleUnFocus() {
    setFocused(false)
  }

  React.useEffect(() => {
    if (focused) {
      ReactEditor.focus(editor)
    }
  }, [focused, editor])

  return (
    <div className={className}>
      {label && (
        <Typography className={classes.label} variant="subtitle2" htmlFor={id}>
          {label}
        </Typography>
      )}

      <Paper className={clsx(classes.canvas, { [classes.canvasFocused]: focused && !preview })}>
        <Slate
          editor={editor}
          value={value}
          onChange={value => {
            setValue(value);
            onChange(JSON.stringify(value));
          }}
        >
          <Grid container justify="space-between" className={classes.toolbar}>
            <Grid item>
              <Tooltip title={preview ? 'Edit Template' : 'Preview Template'} placement="top">
                <IconButton onClick={handleToggleClicked}>
                  {preview ? <EditIcon /> : <RemoveRedEyeIcon />}
                </IconButton>
              </Tooltip>
            </Grid>
            <Grid item>
              <BlockButton format="heading-one" icon={<LooksOneIcon />} title="Toggle heading 1 text format" />
              <BlockButton format="heading-two" icon={<LooksTwoIcon />} title="Toggle heading 2 text format" />

              <MarkButton format="bold" icon={<FormatBoldIcon />} title="Toggle bold text format" />
              <MarkButton format="italic" icon={<FormatItalicIcon />} title="Toggle italic text format" />
              <MarkButton format="underline" icon={<FormatUnderlinedIcon />} title="Toggle underline text format" />

              <BlockButton format="numbered-list" icon={<FormatListNumberedIcon />} title="Toggle numbered list" />
              <BlockButton format="bulleted-list" icon={<FormatListBulletedIcon />} title="Toggle bulleted list" />

              <Tooltip title="Insert a table" placement="top">
                <IconButton>
                  <BorderAllIcon style={{ color: grey[300] }} />
                </IconButton>
              </Tooltip>

              <Tooltip title="Attach an image" placement="top">
                <IconButton>
                  <InsertPhotoIcon style={{ color: grey[300] }} />
                </IconButton>
              </Tooltip>

              <Tooltip title="Insert database field" placement="top">
                <IconButton>
                  <AddIcon style={{ color: grey[300] }} />
                </IconButton>
              </Tooltip>
            </Grid>
          </Grid>
          <div className={clsx(classes.wrapper, { [classes.wrapperPreview]: preview })}>
            <div className={clsx(classes.body, { [classes.bodyPreview]: preview })} onClick={handleFocus}>
              <Editable
                renderElement={renderElement}
                renderLeaf={renderLeaf}
                spellCheck
                disabled={preview}
                onBlur={handleUnFocus}
                onKeyDown={event => {
                  for (const hotkey in HOTKEYS) {
                    if (isHotkey(hotkey, event)) {
                      event.preventDefault();
                      const mark = HOTKEYS[hotkey];
                      toggleMark(editor, mark);
                    }
                  }
                }}
              />
            </div>
          </div>
        </Slate>
      </Paper>
    </div>
  );
}

XEditor.propTypes = {
  className: PropTypes.string,
  id: PropTypes.string.isRequired,
  label: PropTypes.string,
  defaultValue: PropTypes.string,
  onChange: PropTypes.func.isRequired,
};

function MarkButton({ format, icon, ...props }) {
  const editor = useSlate();

  return (
    <Button
      active={isMarkActive(editor, format)}
      onMouseDown={event => {
        event.preventDefault();
        toggleMark(editor, format);
      }}
      {...props}
    >
      {icon}
    </Button>
  );
}

MarkButton.propTypes = {
  format: PropTypes.oneOf(['bold', 'italic', 'underline', 'code']).isRequired,
  icon: PropTypes.node.isRequired,
};

function BlockButton({ format, icon, ...props }) {
  const editor = useSlate();

  return (
    <Button
      active={isBlockActive(editor, format)}
      onMouseDown={event => {
        event.preventDefault();
        toggleBlock(editor, format);
      }}
      {...props}
    >
      {icon}
    </Button>
  );
}

BlockButton.propTypes = {
  format: PropTypes.oneOf(['heading-one', 'heading-two', 'block-quote', 'numbered-list', 'bulleted-list']).isRequired,
  icon: PropTypes.node.isRequired,
};

const toggleBlock = (editor, format) => {
  const isActive = isBlockActive(editor, format);
  const isList = LIST_TYPES.includes(format);

  Transforms.unwrapNodes(editor, {
    match: n => LIST_TYPES.includes(n.type),
    split: true,
  });

  Transforms.setNodes(editor, {
    type: isActive ? 'paragraph' : isList ? 'list-item' : format,
  });

  if (!isActive && isList) {
    const block = { type: format, children: [] };
    Transforms.wrapNodes(editor, block);
  }
};

const toggleMark = (editor, format) => {
  const isActive = isMarkActive(editor, format);

  if (isActive) {
    Editor.removeMark(editor, format);
  } else {
    Editor.addMark(editor, format, true);
  }
};

const isBlockActive = (editor, format) => {
  const [match] = Editor.nodes(editor, {
    match: n => n.type === format,
  });

  return !!match;
};

const isMarkActive = (editor, format) => {
  const marks = Editor.marks(editor);
  return marks ? marks[format] === true : false;
};