import React from 'react'
import { Grid, Typography, Paper, Divider } from '@material-ui/core'

// styles
import classes from './CollectibleHeader.module.scss'

const CollectibleHeader = props => {
  const { data, label, amount } = props
  return (
    <div className={classes.root}>
      <Paper className={classes.container} elevation={0}>
        <Grid container justify='space-between' spacing={2} style={{paddingBottom: '10px'}}>
          <Grid item style={{paddingTop: '10px'}}>
            <div className='header-label'>
              <Typography variant='subtitle2' style={{fontSize: '9px', color: '#5df1b7'}}>{label}</Typography>
            </div>
            <div className='amount'>
              <Typography variant='subtitle1' style={{color: '#3ab77d', fontWeight: 'bold'}}>{amount}</Typography>
            </div>
          </Grid>
          <Grid item >
            <Divider orientation='vertical' />
          </Grid>
          {data && data.map((item, index) => (
            <Grid key={index} item direction='column' justify='center' style={{width: '150px', display: 'flex'}}>
              <div className='header-label' style={{marginLeft: '12%'}}>
                <Typography variant='subtitle2' style={{fontSize: '9px', color: '#f65250'}}>{item.label}</Typography>
              </div>
              <div style={{textAlign: 'center'}}>
                <div className='amount'>
                  <Typography variant='subtitle1' style={{color: '#f65250', fontWeight: 'bold'}}>{item.amount}</Typography>
                </div>
                <div className='period'>
                  <Typography variant='caption' style={{color: '#9ea3ac'}} >{item.period}</Typography>
                </div>
              </div>
            </Grid>
          ))}
        </Grid>
      </Paper>
    </div>
  )
}

export default CollectibleHeader
