import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import BillingCreditMemoServices from 'Services/Api/BillingCreditMemoServices'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll4/ViewAll'

// Style
import classes from './ApprovedCreditMemosTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

// const columnConfig = [
//   { id: 'ticket-list-tile-table-header', name: '' },
//   { id: 'ticket-list-tile-table-header', name: 'ID' },
//   { id: 'ticket-list-tile-table-header', name: 'Due Date' },
//   { id: 'ticket-list-tile-table-header', name: 'Account Name' }
// ]

// Mock data
const mock = require('./../mock-data/collection-ticketList.json')

const ApprovedCreditMemosTile = props => {
  // Constants
  const { service = () => {} } = props

  const [hasCalled, setHasCalled] = React.useState(false)
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Approved Credit Memos',
    variant: 'h6'
  })
  // TODO: Implement if Viewlist otherwise, remove
  const [viewList, setViewList] = React.useState({
    count: 0,
    data: [],
    show: false,
    title: 'Tickets Waiting for Response'
  })

  // Initialize / Watch
  // React.useEffect(() => {
  //   BillingCreditMemoServices.getCreditMemosApproved().then(res => {
  //     let data = null
  //     try {
  //       data = res.data.data[0]
  //     } catch (error) {
  //       data = []
  //     }
  //     setTile(i => ({ ...i, count: data.length, data }))
  //   })
  // }, [])

  // Methods
  // TODO: Implement if Viewlist otherwise, remove
  // TODO: Call Initial service here for viewlist data
  /**
   * API Call for tile data
   * @param service passed down to component as props for api-service
   * @param get service used to fetch data
   */
  const handleOnShowViewList = () => {
    setViewList(i => ({ ...i, show: true }), service())
  }

  const fetchData = () => {
    if(!hasCalled){
      BillingCreditMemoServices.getCreditMemosApproved().then(res => {
        let data = null
        try {
          data = res.data.data[0]
        } catch (error) {
          data = []
        }
        setTile(i => ({ ...i, count: data.length, data }))
      })
      setHasCalled(true)
    }
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props;
    const text = inViewport ? 'In viewport' : 'Not in viewport';
    return (
      <div className="viewport-block" ref={forwardedRef}></div>
    );
  };

  const ViewportBlock = handleViewport(Block)

  return (
    <React.Fragment>
      <ViewportBlock onEnterViewport={() => fetchData()} onLeaveViewport={() => console.log('Approved Credit Memos Tile is not on viewport')} />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tile.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnShowViewList()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{paddingLeft: '6px'}}>Transaction No.</TableCell>
              <TableCell>Date Approved</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tile.data.map((item, index) => (
              <TableRow>
                <TableCell style={{paddingLeft: '6px'}}>{item.transaction.number}</TableCell>
                <TableCell>{moment(item.date.modified).format('MM/DD/YYYY')}</TableCell>
                <TableCell align={'right'} style={{paddingRight: '0'}}>
                  <Link to={`billing/credit/approved/${item.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'approved-credit-memos-modal'}
        config={config}
        tile={'approvedCreditMemos'}
        type='Approved Credit Memos'
        title='List of Approved Credit Memos'
        contents={tile.data}
        viewAll={viewList}
        setViewAll={setViewList}
        count={viewList.count}
        linkCaption='View'
        passProperty={'transaction.number'}
        route='/billing/credit/approved/'
      />
    </React.Fragment>
  )
}

ApprovedCreditMemosTile.displayName = 'DashboardTileComponent'

export default ApprovedCreditMemosTile
