import Grid from '@material-ui/core/Grid'
import CloseIcon from '@material-ui/icons/Close'
import React from 'react'
import { Button, Dialog, Typography } from '@material-ui/core'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import TextField from 'SharedComponents/TextField/CustomTextField'

// Styles
import classes from './ActionDialog.module.scss'

export const SubmitDialog = props => {
  const defaultFunction = function () {}
  const { open = false, handleOnAssign = defaultFunction, handleOnClose = defaultFunction } = props

  return (
    <Dialog onClose={() => handleOnClose(false)} open={open} disableBackdropClick>
      <Grid className={classes['root-assign-dialog']} container direction='column'>
        <Grid item className={classes.title} xs={12}>
          <Grid className={classes['title-wrapper']} container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>Submit Collection Distribution</Typography>
            </Grid>
            <Grid className={classes['title-action']} item>
              <CloseIcon onClick={() => handleOnClose(false)} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item className={classes.details} xs={12}>
          <Typography>Are you sure you want to submit the Collection Distribution?</Typography>
        </Grid>
        <Grid item className={classes.info} xs={12}>
          <Typography>
            This Collection Distribution will be sent to both the Collection Supervisor and Collection Manager for
            approval
          </Typography>
        </Grid>
        <Grid item className={classes.action} xs={12}>
          <Grid container direction='row' justify='space-between'>
            <Grid item xs={5}>
              <Button className={classes.cancel} variant='outlined' onClick={() => handleOnClose(false)}>
                No
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button className={classes.assign} variant='contained' onClick={() => handleOnAssign()}>
                Yes
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Dialog>
  )
}

export const SuccessDialog = props => {
  const defaultFunction = function () {}
  const { open = false, handleOnClose = defaultFunction, handleOnOk = defaultFunction } = props

  return (
    <Dialog onClose={() => handleOnClose(false)} open={open} disableBackdropClick>
      <Grid className={classes['root-assign-success']} container direction='column'>
        <Grid item className={classes.title} xs={12}>
          <Grid className={classes['title-wrapper']} container direction='row'>
            <Grid item xs={12}>
              <Typography variant='h6'>Collection Distribution Submitted</Typography>
            </Grid>
            <Grid className={classes['title-action']} item>
              <CloseIcon onClick={() => handleOnClose(false)} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item className={classes.details} xs={12}>
          <Typography>
            Collection Distribution was successfully submitted to the Collection Supervisor and Collection Manager for
            approval
          </Typography>
        </Grid>
        <Grid item className={classes.action} xs={12}>
          <Grid container direction='row' justify='flex-end'>
            <Grid item xs={5}>
              <Button className={classes.confirm} variant='contained' onClick={() => handleOnOk()}>
                OK
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Dialog>
  )
}
