import React from 'react'
import { Link } from 'react-router-dom'
import { config } from '../../Components/Tiles/ViewAllConfig'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPrint, faList, faShare } from '@fortawesome/free-solid-svg-icons'
import { faCopy } from '@fortawesome/free-regular-svg-icons'

import CloseIcon from '@material-ui/icons/Close'

import {
  TableCell,
  TableBody,
  TableRow,
  Dialog,
  DialogTitle,
  DialogContent,
  RadioGroup,
  Radio,
  FormControlLabel,
  FormHelperText,
  Button,
  IconButton,
  Typography
} from '@material-ui/core'

import { withStyles } from '@material-ui/core/styles'
// Components
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from '../../Components/CustomEnhancedTable/CustomEnhancedTable'

import CustomTextField from 'SharedComponents/TextField/CustomTextField'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import BillingSOAServices from 'Services/Api/BillingSOAServices'

import classes from './SOABatchPrintingPage.module.scss'

export default function SOABatchPrintingPage (props) {
  const headCells = config['soasForPrinting'].headers
  const tableColumnSequence = config['soasForPrinting'].sequence
  const current_user_id = localStorage.getItem('user_id')
  const [SOAsForPrinting, setSOAsForPrinting] = React.useState([])
  const [openModal, setOpenModal] = React.useState(false)
  const [printOptions, setPrintOptions] = React.useState(true)
  const [value, setValue] = React.useState(null)
  const [selectedOpt, setSelectedOpt] = React.useState(null)
  const [error, setError] = React.useState(false)
  const [errorMsg, setErrorMsg] = React.useState('')
  const [atpNo, setATPNo] = React.useState(null)
  const [noOfPages, setNoOfPages] = React.useState(null)
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)

  React.useEffect(() => {
    PageTitleService('Bill Run: SOA Batch for Printing')

    BillingSOAServices.getApprovedClientSOAs({ currentUserId: current_user_id }).then(values => {
      setSOAsForPrinting(values.data.data[0])

      console.group('SOAs For Printing')
      console.log(values.data.data[0])
    })
  }, [])

  const pagination = {
    count: 0,
    rowsPerPage: 0,
    page: 0
  }

  const StyledTableRow = withStyles(theme => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const StyledTableCell = withStyles(theme => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const handlePrintClick = () => {
    setOpenModal(true)
    setPrintOptions(true)
  }

  const handleOnClosePrint = () => {
    setOpenModal(false)
  }

  const handleRadioChange = event => {
    setValue(event.target.value)
    setErrorMsg('')
    setError(false)
  }

  const handleOnOk = event => {
    event.preventDefault()
    if (value === null) {
      setErrorMsg('Please select an option.')
      setError(true)
    } else {
      setErrorMsg('')
      setError(false)
      setPrintOptions(false)

      if (value === 'bir') setSelectedOpt('BIR')
      else setSelectedOpt('Corporate')
    }
  }

  const handleOnChangeATPNo = value => {
    setATPNo(value)
  }

  const handleOnChangeNoOfPages = value => {
    setNoOfPages(value)
  }

  const handleOnPrintSOABatch = () => {
    setOpenSuccessDialog(true)
    setOpenModal(false)
    BillingSOAServices.printSOABatch({ data: { start_no: atpNo, no_of_pages: noOfPages } }).then(values => {
      console.group('SOA Batch')
      console.log(values.data.data)
    })
  }

  const handleOnCloseSuccessModal = () => {
    setOpenSuccessDialog(false)
  }

  const handleOnOKPrint = () => {
    setATPNo(null)
    setNoOfPages(null)
    setOpenSuccessDialog(false)
  }

  return (
    <div>
      <div>
        <SearchTextComponent />
      </div>
      <div className={classes['action-buttons-wrapper']}>
        <div className={classes['export-modify-content']}>
          <FontAwesomeIcon icon={faPrint} className={classes['modify-billing-cycle-icon']} />
          <Link
            id='modify-billing-cycle-link'
            onClick={handlePrintClick}
            className={classes['modify-billing-cycle-link']}
          >
            Print SOA in Batch
          </Link>
          <FontAwesomeIcon icon={faShare} className={classes['modify-billing-cycle-icon']} />
          <Link id='export-link' to={`/`} className={classes['export-link']}>
            Export Batch to a Printable File
          </Link>
          <FontAwesomeIcon icon={faList} className={classes['modify-billing-cycle-icon']} />
          <Link id='modify-billing-cycle-link' to='/' className={classes['modify-billing-cycle-link']}>
            Display Settings
          </Link>
        </div>
      </div>

      <Dialog maxWidth='xs' open={openModal} PaperProps={{ style: { display: 'flex' } }}>
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <IconButton
              aria-label='close'
              id='success-close-button'
              className={classes['success-close-button']}
              onClick={handleOnClosePrint}
            >
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' id='assign-title' className={classes.title}>
              Print SOA {selectedOpt && `(${selectedOpt})`} Batch
            </Typography>
          </div>
        </DialogTitle>
        <DialogContent>
          {printOptions ? (
            <div>
              <Typography>
                Please select SOA batch document you wish to print in batch then click OK to proceed
              </Typography>
              <RadioGroup name='document' value={value} onChange={handleRadioChange}>
                <FormControlLabel value='bir' control={<Radio />} label='SOA (BIR)' />
                <FormControlLabel value='corporate' control={<Radio />} label='SOA (Coporate)' />
              </RadioGroup>
              <FormHelperText>{errorMsg}</FormHelperText>
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-print-cancel-button'
                  className={classes['button-negative']}
                  onClick={handleOnClosePrint}
                >
                  CANCEL
                </Button>
                <Button
                  variant='contained'
                  id='soa-print-ok-button'
                  className={classes['button-positive']}
                  onClick={handleOnOk}
                >
                  OK
                </Button>
              </div>
            </div>
          ) : (
            <div>
              <Typography>Are you sure you want to print the batch Statement of Accounts?</Typography>
              <br />
              <Typography>
                Please enter the starting ATP no. of the pre-printed SOA ({selectedOpt}) form and no. of pages for
                automatic update of the SOA and add the ATP no.
              </Typography>
              <br />
              <div className={classes['atp-fields']}>
                <CustomTextField
                  label='ATP No. (start of batch)'
                  placeholder='Enter Starting ATP No. here'
                  className={classes['atp-no']}
                  value={atpNo}
                  onChange={event => handleOnChangeATPNo(event.target.value)}
                />
                <CustomTextField
                  label='No. of pages'
                  placeholder='Enter no. of pages here'
                  className={classes['no-of-pages']}
                  value={noOfPages}
                  onChange={event => handleOnChangeNoOfPages(event.target.value)}
                />
              </div>
              <br />
              <div className={classes.button}>
                <Button
                  variant='outlined'
                  id='soa-no-button'
                  className={classes['button-negative']}
                  onClick={handleOnClosePrint}
                >
                  No
                </Button>
                <Button
                  variant='contained'
                  id='soa-yes-button'
                  className={classes['button-positive']}
                  onClick={handleOnPrintSOABatch}
                >
                  Yes
                </Button>
              </div>
            </div>
          )}
        </DialogContent>
      </Dialog>

      <Dialog maxWidth='xs' open={openSuccessDialog} PaperProps={{ style: { display: 'flex' } }}>
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <IconButton
              aria-label='close'
              id='success-close-button'
              className={classes['success-close-button']}
              onClick={handleOnCloseSuccessModal}
            >
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' id='assign-title' className={classes.title}>
              Print SOA {selectedOpt && `(${selectedOpt})`} Batch
            </Typography>
          </div>
        </DialogTitle>
        <DialogContent>
          <Typography>The system is now ready to print the SOA ({selectedOpt}) batch</Typography>
          <br />
          <Typography>
            Please ensure that the paper in the printer is placed accordingly and click OK to proceed to print.
          </Typography>
          <div className={classes.button}>
            <Button
              variant='outlined'
              id='soa-success-cancel-button'
              className={classes['button-negative']}
              onClick={handleOnCloseSuccessModal}
            >
              CANCEL
            </Button>
            <Button
              variant='contained'
              id='soa-success-ok-button'
              className={classes['button-positive']}
              onClick={handleOnOKPrint}
            >
              OK
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <CustomEnhancedTable pagination={pagination} tableHeaders={headCells}>
        <TableBody>
          {SOAsForPrinting &&
            SOAsForPrinting.map((item, index) => (
              <StyledTableRow>
                <StyledTableCell>
                  <Typography>{item.billing_cycle_code}</Typography>
                </StyledTableCell>
                <StyledTableCell>
                  <Typography>{item.billing_cycle}</Typography>
                </StyledTableCell>
                <StyledTableCell>
                  <Typography>{item.bill_run_time}</Typography>
                </StyledTableCell>
                <StyledTableCell>
                  <Typography>{item.account_name}</Typography>
                </StyledTableCell>
                <StyledTableCell>
                  <Typography>Successful</Typography>
                </StyledTableCell>
                <StyledTableCell>
                  <Link to={`/billing/soa-pdf/view/${item.soa_number}`}>
                    <Typography variant='caption' className='text-link underline'>
                      <FontAwesomeIcon icon={faCopy} />
                      {item.soa_number}
                    </Typography>
                  </Link>
                </StyledTableCell>
              </StyledTableRow>
            ))}
        </TableBody>
      </CustomEnhancedTable>
    </div>
  )
}
