import React from 'react'
import { Link } from 'react-router-dom'
import _ from 'lodash'
import moment from 'moment'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'

import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import { withStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Grid from '@material-ui/core/Grid'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from '../CustomEnhancedTable/CustomEnhancedTable'
import CircularLoading from "../../../../../SharedComponents/CircularLoading/CircularLoading";
import classes from './ViewAll.module.scss'

export default function CustomizedDialogs (props) {
  const {
    config,
    tile,
    type,
    title,
    contents,
    count,
    show,
    setShow,
    list,
    fetchData,
    params,
    accessors,
    isFetching
  } = props

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  const tableHeaders = config[tile].headers

  const handleOnClose = () => {
    setShow(false)
  }

  const changeView = params => {
    if (fetchData) {
      fetchData(params)
    }
  }

  const mainSearchChange = _.debounce((e) => {
    const params = {
      'search': e.target.value,
      'accessors': accessors
    }
    changeView(params)
  }, 300)

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex', backgroundColor: '#f4f6f8' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography id='modal-title' variant='h6' className={classes.title}>
            {title}
          </Typography>
          <IconButton
            id='modal-close-button'
            aria-label='close'
            className={classes['close-button']}
            onClick={handleOnClose}
          >
            <CloseIcon />
          </IconButton>
          <div className={classes.clear} />
        </div>
      </DialogTitle>
      <DialogContent className={classes['content-container']}>
        <div className={classes['search-container']}>
          <div id='modal-search-field' className={classes['search-field']}>
            <SearchTextComponent onChange={e => {
              e.persist()
              mainSearchChange(e)
            }} />
          </div>
          <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
            <Grid container id='display-settings-icon'>
              <ViewListRoundedIcon className={classes['display-settings-icon']} />
              <Typography className='text-link underline'>Display Settings</Typography>
            </Grid>
          </Link>
        </div>

        <CustomEnhancedTable
          count={count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
          fetchData={fetchData}
          params={params}
        >
          <TableBody>
            {isFetching && <StyledTableRow>
              <StyledTableCell component='th' colspan={10}>
                <CircularLoading />
              </StyledTableCell>
            </StyledTableRow>}
            {!isFetching && contents && contents.map((row, contentsIndex) => (
              <StyledTableRow key={contentsIndex}>
                <StyledTableCell component='th'>
                  <Typography variant='caption' align='left'>
                    { type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.client.account_number : '' }
                    { type === 'Approved Invoices' || type === 'Pending Invoices' || type === 'Invoice Submitted' ? DateFormatterByAbbreviation(
                      new Date(moment(row.transaction.date).subtract(parseInt(row.transaction.date
                        .substring(11, 13)), 'h'))) : null }
                    {type === 'TPA Monitoring' ? row.client.account_number : ''}
                  </Typography>
                </StyledTableCell>
                <StyledTableCell component='th'>
                  <Typography variant='caption' align='left'>
                    { type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.client.account_name : '' }
                    { type === 'Approved Invoices' || type === 'Pending Invoices' || type === 'Invoice Submitted' ? row.transaction.number : null}
                    {type === 'TPA Monitoring' ? row.client.account_name : ''}
                  </Typography>
                </StyledTableCell>
                <StyledTableCell component='th' align='left'>
                  <Typography variant='caption' align='left'>
                    { type === 'Assign Analyst' || type === 'Setup Billing Cycle' ? row.contract.type : '' }
                    { type === 'Approved Invoices' || type === 'Pending Invoices' || type === 'Invoice Submitted' ? row.billing_client_contract[0].client.account_name : null}
                    {type === 'TPA Monitoring' ? row.contract.type : ''}
                  </Typography>
                </StyledTableCell>
                <StyledTableCell component='th'>
                  <Typography variant='caption' align='left'>

                    { type === 'Assign Analyst' ? DateFormatterByAbbreviation(
                      new Date(moment(row.contract.effectivity).subtract(parseInt(row.contract.effectivity
                        .substring(11, 13)), 'h'))) : null }

                    { type === 'Setup Billing Cycle' ? DateFormatterByAbbreviation(
                      new Date(moment(row.contract.effectivity).subtract(parseInt(row.contract.effectivity
                        .substring(11, 13)), 'h'))) : null }

                    { type === 'Approved Invoices' ? row.approved_by : null}
                    {/* Date here should be date submitted, put date.modified as temp workaround */}
                    { type === 'Pending Invoices' ? DateFormatterByAbbreviation(
                      new Date(moment(row.date.modified).subtract(parseInt(row.date.modified
                        .substring(11, 13)), 'h'))) : null }
                    {/* Date here should be date submitted, put date.modified as temp workaround */}
                    { type === 'Invoice Submitted' ? DateFormatterByAbbreviation(
                      new Date(moment(row.date.modified).subtract(parseInt(row.date.modified
                        .substring(11, 13)), 'h'))) : null }
                    {type === 'TPA Monitoring' ? parseInt((row.contract.tpa.rsd - row.contract.tpa.rsd_usage) / row.contract.tpa.rsd * 100) + '%' : ''}
                  </Typography>
                </StyledTableCell>

                {type === 'Approved Invoices'
                  ? <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      { type === 'Approved Invoices' ? DateFormatterByAbbreviation(
                        new Date(moment(row.date.modified).subtract(parseInt(row.date.modified
                          .substring(11, 13)), 'h'))) : '' }
                    </Typography>
                  </StyledTableCell>
                  : null
                }

                {type === 'TPA Monitoring'
                  ? <StyledTableCell component='th'>
                    <Typography variant='caption' align='left'>
                      {type === 'TPA Monitoring' ? row.invoice ? moment(row.invoice.date.created).format('MMM DD, YYYY') : '' : ''}
                    </Typography>
                  </StyledTableCell>
                  : null
                }

                { type === 'Pending Invoices' || type === 'Approved Invoices' || type === 'Invoice Submitted'
                  ? <StyledTableCell component='th' align='right'>
                    <Typography variant='caption'>
                      { CurrencyFormatter(row.summary.sub_total) }
                    </Typography>
                  </StyledTableCell>
                  : null
                }

                <StyledTableCell component='th' align='right'>
                  { type === 'Assign Analyst' && <Link to={`/clients/assign/${row._id}/${row.client.account_number}`}>
                    <Typography variant='caption' className='text-link underline'>Setup</Typography></Link> }

                  { type === 'Setup Billing Cycle' && <Link to={`/clients/analyze/${row._id}/${row.client.account_number}`}>
                    <Typography variant='caption' className='text-link underline'>Setup</Typography></Link> }

                  { type === 'Pending Invoices' && <Link to={`/billing/invoice/pending/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>Edit</Typography></Link> }

                  { type === 'Invoice Submitted' && <Link to={`/billing/invoice/forapproval/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>View</Typography></Link> }

                  { type === 'Approved Invoices' && <Link to={`/billing/invoice/approved/${row.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>View</Typography></Link> }
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </CustomEnhancedTable>
      </DialogContent>
    </Dialog>
  )
}
