import React from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Typography from '@material-ui/core/Typography';
import noop from 'lodash/noop';

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent';
import TextField from '../../../Components/FormComponents/TextField/CustomTextField';
import PageFooter from '../../../Components/PageFooter/PageFooter';
import Editor from './Editor/Editor';
import DEFAULT_VALUE from './defaultLetterTemplate';
import classes from './CreateLetterTemplate.module.scss';

export default function CreateLetterTemplate() {
  const history = useHistory();
  const [willPublish, setWillPublish] = React.useState(false);
  const [values, setValues] = React.useState({
    letterType: '',
    category: '',
    templateName: '',
    description: ''
  });

  const handleInputChange = event => {
    event.persist();

    setValues(values => ({
      ...values,
      [event.target.name]: event.target.value
    }))
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['collectionMaintenance', 'createLetterTemplate'])
  })

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction="column" justify="space-between">
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant="h5">
                Create Letter Template
              </Typography>
            </Grid>
          </Grid>

          <Grid style={{ marginTop: 50 }} container>
            <Grid item xs={8}>
              <Grid container justify="space-between">
                <Grid item xs={5}>
                  <FormControl className={classes.formControl}>
                    <InputLabel id="letter-type-label">Letter Type</InputLabel>
                    <Select
                      labelId="letter-type-label"
                      id="letter-type"
                      name="letterType"
                      value={values.letterType}
                      onChange={handleInputChange}
                    >
                      <MenuItem value="">
                        <em>Select Letter Type</em>
                      </MenuItem>
                      <MenuItem value="reminder">Reminder Letter</MenuItem>
                      <MenuItem value="suspension">Suspension Letter</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>

                <Grid item xs={5}>
                  <FormControl className={classes.formControl}>
                    <InputLabel id="category-label">Category</InputLabel>
                    <Select
                      labelId="category-label"
                      id="category"
                      name="category"
                      value={values.category}
                      onChange={handleInputChange}
                    >
                      <MenuItem value="">
                        <em>Select Category</em>
                      </MenuItem>
                      <MenuItem value="client">Client</MenuItem>
                      <MenuItem value="member">Member</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>
              </Grid>

              <Grid style={{ marginTop: 30 }} container justify="space-between">
                <Grid item xs={5}>
                  <TextField
                    id="template-name"
                    label="Template Name"
                    name="templateName"
                    placeholder="Enter letter template name"
                    value={values.templateName}
                    onChange={handleInputChange}
                    required
                  />
                </Grid>
              </Grid>

              <Grid container justify="space-between">
                <Grid item xs={12}>
                  <TextField
                    multiline
                    rows={3}
                    id="description"
                    label="Description"
                    name="description"
                    placeholder="Enter letter template description"
                    value={values.description}
                    onChange={handleInputChange}
                  />
                </Grid>
              </Grid>

              <Grid container justify='space-between'>
                <Grid item xs={12}>
                  <Editor label="Letter Template" defaultValue={JSON.stringify(DEFAULT_VALUE)} />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter 
            showCreateLetterTemplateBtn 
            handleOnCancelClicked={() => history.push('/collection/maintenance')} 
            handleSaveLetterTemplate={noop} 
            publishLetterTemplate={willPublish}
            handlePublishLetterTemplate={() => setWillPublish(true)} 
            handlePublishLetterTemplateConfirmation={() => setWillPublish(false)}
            handlePublishLetterTemplateCancellation={() => setWillPublish(false)}
          />
        </Grid>
      </Grid>
    </div>
  )
}
