import React from 'react'
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent';
import PageFooter from '../../Components/PageFooter/PageFooter';
import classes from './CollectionMaintenance.module.scss';

export default function CollectionMaintenance() {
  const history = useHistory()

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['collectionMaintenance'])
  })

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction="column" justify="space-between">
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant="h5">
                Collection Maintenance
              </Typography>
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter 
            showCollectionMaintenanceBtn 
            handleCreateLetterTemplate={() => history.push('/collection/maintenance/create-letter-template')} 
          />
        </Grid>
      </Grid>
    </div>
  )
}
