import Grid from '@material-ui/core/Grid'
import React from 'react'
import { Checkbox, Typography, Link, TableRow, TableCell } from '@material-ui/core'
import RemoveCircleRoundedIcon from '@material-ui/icons/RemoveCircleRounded'

// Components
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import { CustomTable } from '../../../../Components/CustomTable/index'
import SoaMenu from './../../../../Components/SoaMenu'

// Services
import moment from 'moment'

// Styles
import classes from './AnalyzeDetailsComponent.module.scss'

// Mock
const dataTable = require('./mock-data/table-data.json')

const headCells = require('./mock-data/table-head.js')

export default function AnalyzeDetailsComponent (props) {
  const defaultFunction = function () {}
  const {
    handleOnAddNewSOA = defaultFunction,
    handleOnAddSOAClicked = defaultFunction,
    handleOnRemoveMember = defaultFunction,
    soa = [],
    availableSoa = []
  } = props

  const [_state, _setState] = React.useState({
    body: [],
    count: 20,
    filter: {},
    header: headCells.headCells,
    limit: 1,
    order: true,
    orderBy: 'account_no',
    page: 1,
    soa_number: '',
    sort: ''
  })

  const [slider, setSlider] = React.useState({ count: 0, cut: 3, index: 0, selected: 0 })
  const [member, setMember] = React.useState({})

  React.useEffect(() => {
    const selectedSoa = soa[slider.index + slider.selected]

    _setState(i => ({
      ...i,
      body: selectedSoa ? selectedSoa.member : [],
      count: selectedSoa ? selectedSoa.member.length : 0,
      soa_number: selectedSoa ? selectedSoa.soa_number : ''
    }))
  }, [soa, slider])

  const handleOnChanges = params => {
    setSlider(i => ({ ...i, ...params }))
  }

  const handleOnRemoveSoa = params => {
    // TODO
    console.log(params)
  }

  const handleOnSelectAll = params => {
    const selected = {}
    _state.body.map((key, i) => {
      selected[i] = params
    })
    setMember(i => ({ ...i, ...selected }))
  }

  const handleRemoveMember = () => {
    const _data = {}
    _data.soa_number = _state.soa_number
    _data.member = []

    Object.keys(member).map(key => {
      if (member[key]) _data.member.push(_state.body[key]._id)
    })

    handleOnRemoveMember(_data)
  }

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Typography variant='h6'>Collection Reference</Typography>
      </Grid>
      <Grid item xs={12}>
        <Grid container direction='row'>
          <Grid item xs>
            <FormSlider details={soa} OnChanges={e => handleOnChanges(e)} OnRemove={e => handleOnRemoveSoa(e)} />
          </Grid>
          <Grid item>
            <Grid container direction='column' justify='flex-end' style={{ height: '100%' }}>
              <Grid item>
                <SoaMenu
                  details={availableSoa || []}
                  onAddClicked={e => handleOnAddSOAClicked(e)}
                  onSelect={e => handleOnAddNewSOA(e)}
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} style={{ marginTop: '20px' }}>
        <Link
          component='button'
          variant='body2'
          onClick={() => {
            handleRemoveMember()
          }}
        >
          <Grid container direction='row'>
            <Grid item>
              <Grid container direction='column' justify='center' style={{ height: '100%' }}>
                <RemoveCircleRoundedIcon style={{ fontSize: '15px', marginRight: '5px' }} />
              </Grid>
            </Grid>
            <Grid item>
              <Typography>Remove</Typography>
            </Grid>
          </Grid>
        </Link>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <CustomTable
          handleSelectAll={e => handleOnSelectAll(e.target.checked)}
          showCheckBox
          data={_state}
          setData={_setState}
          showTextField
          body={_data =>
            _data.map(
              (
                { account_no, last_name, first_name, middle_initial, renewal_date, end_date, plan_type_name },
                index
              ) => (
                <TableRow key={`detail-list-item-${index}`}>
                  <TableCell id={`account-no-${index}`}>
                    <Checkbox
                      checked={member ? member[index] : false}
                      onClick={e => {
                        setMember(i => ({ ...i, [index]: e.target.checked }))
                      }}
                      color='inherit'
                      inputProps={{ 'aria-label': 'primary checkbox' }}
                    />
                  </TableCell>
                  <TableCell id={`detail-id-${index}`}>{index + 1}</TableCell>
                  <TableCell style={{ whiteSpace: 'nowrap' }} id={`detail-member-id-${index}`}>
                    {account_no}
                  </TableCell>
                  <TableCell id={`detail-last-name-${index}`}>{last_name}</TableCell>
                  <TableCell id={`detail-first-name-${index}`}>{first_name}</TableCell>
                  <TableCell id={`detail-middle-initial-${index}`}>{middle_initial}</TableCell>
                  <TableCell id={`detail-ref-date-${index}`}>{moment(renewal_date).format('ll')}</TableCell>
                  <TableCell id={`detail-end-date-${index}`}>{moment(end_date).format('ll')}</TableCell>
                  <TableCell id={`detail-contract-type-${index}`}>{plan_type_name}</TableCell>
                </TableRow>
              )
            )
          }
        />
      </Grid>
    </Grid>
  )
}
