import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';

Element.propTypes = {
  element: PropTypes.shape({
    type: PropTypes.oneOf([
      'block-quote',
      'bulleted-list',
      'heading-one',
      'heading-two',
      'list-item',
      'numbered-list',
      'paragraph',
    ]),
  }),
  attributes: PropTypes.object,
  children: PropTypes.object,
};

export default function Element({ attributes, children, element }) {
  switch (element.type) {
    case 'block-quote':
      return <blockquote {...attributes}>{children}</blockquote>;
      
    case 'bulleted-list':
      return <ul {...attributes}>{children}</ul>;

    case 'heading-one':
      return <Typography variant="h1" {...attributes}>{children}</Typography>;

    case 'heading-two':
      return <Typography variant="h2" {...attributes}>{children}</Typography>;

    case 'list-item':
      return <li {...attributes}>{children}</li>;

    case 'numbered-list':
      return <ol {...attributes}>{children}</ol>;

    default:
      return <Typography {...attributes}>{children}</Typography>;
  }
}