import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import { config } from '../ViewAllConfig'
import ViewAll from '../../ViewAll4/ViewAll'

// Style
import classes from './ValidatedTransactionsTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

// const columnConfig = [
//   { id: 'ticket-list-tile-table-header', name: '' },
//   { id: 'ticket-list-tile-table-header', name: 'ID' },
//   { id: 'ticket-list-tile-table-header', name: 'Due Date' },
//   { id: 'ticket-list-tile-table-header', name: 'Account Name' }
// ]

const ValidatedTransactionsTile = props => {
  // Constants
  const { services = {} } = props

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'name',
    order: true
  }

  const [hasCalled, setHasCalled] = React.useState(false)
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Validated Transactions',
    variant: 'h6'
  })
  // TODO: Implement if Viewlist otherwise, remove

  const [viewList, setViewList] = React.useState({
    count: 0,
    data: [],
    show: false,
    title: 'Tickets Waiting for Response'
  })

  const handleOnShowViewList = () => {
    setViewList({ ...initialValue, show: true, title: 'List of Client Transaction Journals to Setup' })
  }

  const fetchData = () => {
    if (!hasCalled) {
      services.BillingClientInvoicesServices.getInvoicesValidated().then(res => {
        services.BillingCreditMemoServices.getCreditMemosValidated().then(res2 => {
          services.BillingDebitMemoServices.getDebitMemosValidated().then(res3 => {
            let data = []
            let invoice = null
            let credit = null
            let debit = null
            try {
              invoice = res.data.data[0]
              credit = res2.data.data[0]
              debit = res3.data.data[0]
              data = [...invoice, ...credit, ...debit]
              console.log(data)
            } catch (error) {
              data = []
            }
            console.log(data)
            setTile(i => ({ ...i, count: data.length, data }))
          })
        })
      })
      setHasCalled(true)
    }
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => fetchData()}
        onLeaveViewport={() => console.log('Validated Transaction Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tile.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnShowViewList()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Account Name</TableCell>
              <TableCell>Transaction Type</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tile.data.map((item, index) => (
              <TableRow>
                <TableCell style={{ paddingLeft: '6px' }}>{item.client.account_name}</TableCell>
                <TableCell>{item.transaction.type}</TableCell>
                <TableCell align={'right'} style={{ paddingRight: '0' }}>
                  <Link to={`/billing/validated/transactions/${item.transaction.number}`}>
                    <Typography variant='caption' className='text-link underline'>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'debit-memos-submitted-for-approval-modal'}
        config={config}
        tile={'validatedTransactions'}
        type='Validated Transactions'
        title='List of Validated Transactions'
        contents={tile.data}
        viewAll={viewList}
        setViewAll={setViewList}
        count={viewList.count}
        linkCaption='View'
        passProperty={'transaction.number'}
        route='/billing/debit/forapproval/'
      />
    </React.Fragment>
  )
}

ValidatedTransactionsTile.displayName = 'DashboardTileComponent'

export default ValidatedTransactionsTile
