import React from 'react'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import PageFooter from './../../../Components/PageFooter/PageFooter'
import SearchTextComponent from './../../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from './../../../Components/CustomTable'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Mock Data
import body from '../mock-data/approved-suspensions-list.json'

// Styles
import classes from './ApprovedSuspensionList.module.scss'

export default function ApprovedSuspensionList() {
  const [approvedSuspensions, setApprovedSuspensions] = React.useState({
    body,
    count: 20,
    filter: {},
    header,
    limit: 1,
    order: true,
    orderBy: null,
    page: 1,
    sort: ''
  })

  const handleDisplaySettingsClicked = () => {
    console.log('initial function')
  }

  const handleCloseClicked = () => {
    console.log('closed clicked')
  }

  const handleSearchChanged = _data => {
    console.log(_data)
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['listOfApprovedSuspensions'])
  })

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header}>
            <Grid item>
              <Typography variant='h5'>
                List of Approved Suspensions
              </Typography>
            </Grid>
          </Grid>
          <Grid container className={classes.search}>
            <Grid item xs>
              <SearchTextComponent
                placeholder='Search List of Suspensions'
                displaySettings
                displaySettingsTitle='Approved Date Range*'
                handleOnDisplaySettingsClicked={handleDisplaySettingsClicked}
                onChange={handleSearchChanged}
              />
            </Grid>
          </Grid>

          <Grid container>
            <Grid item xs>
              <Table
                hasLink
                data={approvedSuspensions}
                setData={setApprovedSuspensions}
                body={_data =>
                  _data.map((item, index) => (
                    <TableRow key={`submitted-suspension-list-item-${index}`}>
                      <TableCell id={`submitted-suspension-category-${index}`}>
                        {item.category}
                      </TableCell>
                      <TableCell id={`submitted-suspension-ref-number-${index}`}>
                        {item.suspension_no}
                      </TableCell>
                      <TableCell id={`submitted-suspension-account-name-${index}`}>{item.account_name}</TableCell>
                      <TableCell id={`submitted-suspension-date-submitted-${index}`}>{item.date_approved}</TableCell>
                      <TableCell id={`submitted-suspension-outstanding-soa-amount${index}`}>
                        {CurrencyFormatter(item.outstanding_soa_amount)}
                      </TableCell>
                      <TableCell id={`submitted-suspension-view-link-${index}`}>
                        <Typography
                          id={`submitted-suspension-item-action-${index}`}
                          variant='caption'
                          className='text-link underline'
                        >
                          View
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))
                }
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <PageFooter isControl handleOnCloseClicked={handleCloseClicked} />
        </Grid>
      </Grid>
    </div>
  )
}

const header = [
  { id: 'category', name: 'Category', sortField: 'category', match: 'any' },
  { id: 'suspension-no', name: 'Suspension No.', sortField: 'suspension_no', match: 'any' },
  { id: 'account-name', name: 'Account Name', sortField: 'account_name', match: 'any' },
  { id: 'date-approved', name: 'Date Approved', sortField: 'date_approved', match: 'date' },
  { id: 'outstanding-soa-amount', name: 'Outstanding SOA Amount', sortField: 'outstanding_soa_amount', match: 'date' },
]