import React, { useState, useEffect } from 'react'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

import SearchTextComponent from 'Pages/Protected/BillingAndCollection/Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from 'Pages/Protected/BillingAndCollection/Components/CustomTable'

import { Link } from 'react-router-dom'

import Grid from '@material-ui/core/Grid'
import { IconButton, TableCell, TableRow } from '@material-ui/core'
import ReplyIcon from '@material-ui/icons/Reply'
import Typography from '@material-ui/core/Typography'

import { reply } from './MemberListAgingSummaryPage.module.scss'

import header from './config/config'

const mock = require('./mock-data/mock.json')

const MemberListAgingSummaryPage = () => {
  const [state, setState] = useState({
    body: mock,
    count: 20,
    filter: {},
    header: header,
    limit: 1,
    order: true,
    orderBy: 'account_no',
    page: 1,
    sort: ''
  })

  useEffect(() => {
    PageTitleService('List of Members with Aging Summary')
    BreadcrumbsService.setSelectedItems(['collectionDistributionDashboard', 'listOfMembersWithAgingSummary'])
  }, [])

  const displaySettingsHandler = () => {
    console.log('display settings clicked')
  }
  const searchChangeHandler = _data => {
    console.log('search change handler: ' + _data)
  }

  const searchProps = {
    placeholder: 'Search List of Members with Aging Summary',
    displaySettings: true,
    handleOnDisplaySettingsClicked: displaySettingsHandler,
    onChange: searchChangeHandler,
    exportFiles: true
  }

  const renderBody = _data =>
    _data.map(({ member_id,
      member_name,
      account_name,
      no_of_open_soa,
      collection_analyst,
      total_amount_due, i }) => (
        <TableRow key={i}>
          <TableCell id={`account-no-${i}`}>{member_id}</TableCell>
          <TableCell id={`account-no-${i}`}>{member_name}</TableCell>
          <TableCell id={`account-no-${i}`}>{account_name}</TableCell>
          <TableCell id={`account-no-${i}`}>{no_of_open_soa}</TableCell>
          <TableCell id={`account-no-${i}`}>{collection_analyst}</TableCell>
          <TableCell id={`account-no-${i}`}>{total_amount_due}</TableCell>
          <TableCell id={`account-no-${i}`}>
            <Typography
              id={`account-no-${i}`}
              variant='caption'
              className={`text-link underline`}
              onClick={i => {
                console.log('View clicked:' + i)
              }}
            >
              View
          </Typography>
          </TableCell>
        </TableRow>
      ))

  const tableProps = {
    data: state,
    setData: setState,
    body: renderBody,
    hasLink: true
  }

  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <SearchTextComponent {...searchProps} />
      </Grid>
      <Grid item xs={12}>
        <Table {...tableProps} />
      </Grid>
    </Grid>
  )
}

export default MemberListAgingSummaryPage
