// transaction types and tax types
import data from '../Data'

const tableHeaders = [
  {
    id: 'pending-invoice-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Choose Transaction Type',
    center: true
  },
  {
    id: 'pending-invoice-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member Account ID',
    center: true
  },
  {
    id: 'pending-invoice-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member Name',
    center: true
  },
  {
    id: 'pending-invoice-description',
    numeric: false,
    disablePadding: false,
    label: 'Enter Description',
    center: true
  },
  {
    id: 'pending-invoice-tax-type',
    numeric: false,
    disablePadding: false,
    label: 'Choose Tax Type',
    center: true
  },
  {
    id: 'pending-invoice-amount',
    numeric: false,
    disablePadding: false,
    label: 'Choose Taxable Amount',
    center: true
  },
  {
    id: '',
    numeric: false,
    disablePadding: false,
    label: '',
    center: true
  }
]

export default function SearchClientContract (props) {
  const history = useHistory()
  const { clientContract, setStep, handlBackToSelectClientContract, handlBackToSelectTransactionType, clientContractId } = props

  const clientInfo = clientContract.data.client
  const billingInfo = clientContract.data.billing
  const contractInfo = clientContract.data.contract
  const [disabledSave, setDisabledSave] = React.useState(false)
  const [openSaveDialog, setOpenSaveDialog] = React.useState(false)
  const pagination = {
    count: 0,
    rowsPerPage: 0,
    page: 0
  }
  const location =
  contractInfo.soa_send_to_address.unit + ' ' +
  contractInfo.soa_send_to_address.floor + 'F ' +
  contractInfo.soa_send_to_address.bldg_no + ' ' +
  contractInfo.soa_send_to_address.bldg_name + ' Bldg. ' +
  contractInfo.soa_send_to_address.street + ', ' +
  ' Brgy. ' + contractInfo.soa_send_to_address.brgy + ', ' +
  contractInfo.soa_send_to_address.region + ' ' +
  contractInfo.soa_send_to_address.zip_code

  var today = new Date()
  var dd = String(today.getDate()).padStart(2, '0')
  var mm = String(today.getMonth() + 1).padStart(2, '0') // January is 0!
  var yyyy = today.getFullYear()

  today = mm + '/' + dd + '/' + yyyy

  const listOfTransactionType = data.TransactionType.Invoice
  const listOfTaxType = data.TaxType

  const [invoiceNumber, setInvoiceNumber] = React.useState('INV000000#')
  const [totalTaxableAmount, setTotalTaxableAmount] = React.useState(0)
  const [totalTax, setTotalTax] = React.useState(0)
  const [totalAmount, setTotalAmount] = React.useState(0)

  // TextField values
  const [transactionType, setTransactionType] = React.useState('')
  const [memberAccountId, setMemberAccountId] = React.useState('')
  const [memberName, setMemberName] = React.useState('')
  const [description, setDescription] = React.useState('')
  const [taxType, setTaxType] = React.useState('')
  const [taxableAmount, setTaxableAmount] = React.useState('')
  // edit Textfield values
  const [editTransactionType, setEditTransactionType] = React.useState('')
  const [editMemberAccountId, setEditMemberAccountId] = React.useState('')
  const [editMemberName, setEditMemberName] = React.useState('')
  const [editDescription, setEditDescription] = React.useState('')
  const [editTaxType, setEditTaxType] = React.useState('')
  const [editTaxableAmount, setEditTaxableAmount] = React.useState('')
  // TextField Error messages
  const [transactionTypeErrorMessage, setTransactionTypeErrorMessage] = React.useState('')
  const [descriptionErrorMessage, setDescriptionErrorMessage] = React.useState('')
  const [taxTypeErrorMessage, setTaxTypeErrorMessage] = React.useState('')
  const [taxableAmountErrorMessage, setTaxableAmountErrorMessage] = React.useState('')

  const [fields, setFields] = React.useState([])

  // add field
  const handleAddField = () => {
    let check = true
    if (!transactionType) {
      setTransactionTypeErrorMessage('Please choose the transaction type')
      check = false
    }
    if (!description) {
      setDescriptionErrorMessage('Please enter Description')
      check = false
    }
    if (!taxType) {
      setTaxTypeErrorMessage('Please choose tax type')
      check = false
    }
    if (!taxableAmount) {
      setTaxableAmountErrorMessage('Please enter the amount')
      check = false
    }
    if (check) {
      const newEntry = {
        billingCodeType: transactionType,
        memberAccountId: memberAccountId,
        memberName: memberName,
        description: description,
        taxType: taxType,
        taxableAmount: taxableAmount,
        canEdit: false
      }
      let tempFields = [...fields]
      tempFields.reverse()
      tempFields.push(newEntry)
      tempFields.reverse()
      // setFields(fields.concat(newEntry))
      setFields(tempFields)
      // tempFields.push(newEntry)
      let subTotal = 0
      let taxAmount = 0
      for (const field of tempFields) {
        subTotal += parseInt(field.taxableAmount)
      }
      taxAmount = subTotal * 0.12
      setTotalTaxableAmount(subTotal)
      setTotalTax(taxAmount)
      setTotalAmount(subTotal + taxAmount)
      // clear fields
      setTransactionType('')
      setMemberAccountId('')
      setMemberName('')
      setDescription('')
      setTaxType('')
      setTaxableAmount('')
      setTransactionTypeErrorMessage('')
      setDescriptionErrorMessage('')
      setTaxTypeErrorMessage('')
      setTaxableAmountErrorMessage('')
    }
  }
  // delete field
  const handleRemoveField = (index) => {
    setFields(entires => entires.filter((fields, i) => i !== index))
    let tempFields = fields
    tempFields.splice(index, 1)
    console.log(tempFields)
    let subTotal = 0
    let taxAmount = 0
    for (const field of tempFields) {
      subTotal += parseInt(field.taxableAmount)
    }
    taxAmount = subTotal * 0.12
    setTotalTaxableAmount(subTotal)
    setTotalTax(taxAmount)
    setTotalAmount(subTotal + taxAmount)
  }
  const handleEditField = (index, status) => {
    let newArr = [...fields]
    newArr[index].canEdit = status
    if (status) {
      setEditTransactionType(newArr[index].billingCodeType)
      setEditMemberAccountId(newArr[index].memberAccountId)
      setEditMemberName(newArr[index].memberName)
      setEditDescription(newArr[index].description)
      setEditTaxType(newArr[index].taxType)
      setEditTaxableAmount(newArr[index].taxableAmount)
      setFields(newArr)
    } else {
      setEditTransactionType('')
      setEditMemberAccountId('')
      setEditMemberName('')
      setEditDescription('')
      setEditTaxType('')
      setEditTaxableAmount('')
      setFields(newArr)
    }
  }
  // update field
  const handleUpdateField = (index) => {
    console.log(editTransactionType)
    console.log(editMemberAccountId)
    console.log(editMemberName)
    console.log(editDescription)
    console.log(editTaxType)
    console.log(editTaxableAmount)
    let newArr = [...fields]
    newArr[index].canEdit = false
    newArr[index].billingCodeType = editTransactionType
    newArr[index].memberAccountId = editMemberAccountId
    newArr[index].memberName = editMemberName
    newArr[index].description = editDescription
    newArr[index].taxType = editTaxType
    newArr[index].taxableAmount = editTaxableAmount
    let subTotal = 0
    let taxAmount = 0
    for (const field of newArr) {
      subTotal += parseInt(field.taxableAmount)
    }
    taxAmount = subTotal * 0.12
    setTotalTaxableAmount(subTotal)
    setTotalTax(taxAmount)
    setTotalAmount(subTotal + taxAmount)
    setFields(newArr)
  }

  // Save Button, create pending invoice
  const handleSaveInvoice = () => {
    let subTotal = 0
    let taxAmount = 0
    let entityType = 'Client'
    let transcationType = 'Invoice'
    let transactionStatus = 'New'
    let submittedBy = 'User'
    let details = []
    let detail = {}
    for (const field of fields) {
      console.log(field)
      let [code, name] = field.billingCodeType.split('  ')
      let [first, middle, last] = field.memberName.split(' ')
      subTotal += parseFloat(field.taxableAmount)
      detail = {
        memberAccountId: field.memberAccountId,
        billingCode: code,
        billingType: name,
        firstName: first,
        middleName: middle,
        lastName: last,
        description: field.description,
        value: field.taxableAmount
      }
      details.push(detail)
    }
    console.log(details)
    taxAmount = subTotal * 0.12

    let data = {
      // header
      clientContractId: clientContractId,
      subTotal: subTotal,
      taxType: clientContract.data.client.tax_type,
      taxAmount: taxAmount,
      entityType: entityType,
      transcationType: transcationType,
      transactionNumber: invoiceNumber,
      transactionStatus: transactionStatus,
      submittedBy: submittedBy,
      // details
      details: details
    }
    BillingClientInvoicesServices.createInvoice({data: data})
    history.push('/')
  }
  // For Modify Bill Run inclusion Popper
  const [anchorEl, setAnchorEl] = React.useState(null)
  const handleClick = (event) => {
    if (event.target.value === 'x' || event.target.value === 'Cancel') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }
  const open = Boolean(anchorEl)
  const id = open ? 'transitions-popper' : undefined
  const [value, setValue] = React.useState('Include in the upcoming bill run')
  const handleChange = (event) => {
    setValue(event.target.value)
  }

  return (
    <div>
      <HeaderDetails>
        <Typography id='header-details-create-transaction' variant='h5'>
          Create Transaction
        </Typography>
      </HeaderDetails>
      <FormDrawer
        id='form-drawer'
        drawerHeight={'570px'}
        drawerContent={
          <div>
            <Typography variant='h6'>{clientInfo.account_name}
              <RemoveCircleIcon onClick={() => handlBackToSelectClientContract()} color='secondary' className={classes['remove-icon']} /></Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value'>{clientInfo.account_number}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value'>{clientInfo.account_status}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>{contractInfo.payment_term}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value'>{clientInfo.tax_type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                ACCOUNT TIN
              </Typography>
              <Typography id='drawer-corporate-tin-value'>{clientInfo.tin}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value'>
                {billingInfo.cycle.code + ' - ' + billingInfo.cycle.name}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT PLAN TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value'>{contractInfo.type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value'>
                {moment(contractInfo.effectivity).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value'>
                {moment(contractInfo.expiration).format('MMM DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value'>{location}</Typography>
            </div>
          </div>
        }
      >
        <div className={classes['modify-bill-run-inclusion']}>
          <div className={classes['modify-bill-run-inclusion-content']}>
            <Link id='view-billing-link' aria-describedby={id} onClick={handleClick} className={classes['view-link']}>
              <FontAwesomeIcon id='view-billing-icon' className={classes['view-icon']} icon={faEdit} />
              Modify Bill Run Inclusion
            </Link>
            <Popper id={id} open={open} anchorEl={anchorEl} transition>
              {({ TransitionProps }) => (
                <Fade {...TransitionProps} timeout={350}>
                  <div
                    style={{
                      padding: '10px',
                      'background-color': 'white',
                      'box-shadow': '5px 5px rgb(0,0,0,10%)'

                    }}
                  >
                    <div>
                      <span>Select Bill Run inlcusion for this transaction<span style={{'font-size': '22px', 'padding-left': '15px', 'padding-right': '5px', cursor: 'pointer'}} onClick={e => handleClick(e)}>X</span></span>
                    </div>
                    <div>
                      <RadioGroup aria-label='gender' name='gender1' value={value} onChange={handleChange}>
                        <FormControlLabel value='Include in the upcoming bill run' control={<Radio color='primary' />} label='Include in the upcoming bill run' />
                        <FormControlLabel value='Postpone to the next bill run' control={<Radio color='primary' />} label='Postpone to the next bill run' />
                      </RadioGroup>
                    </div>
                    <div>
                      <Button
                        id='submitted-link'
                        variant='contained'
                        onClick={e => handleClick(e)}
                        style={{color: '#3ab77d',
                          'background-color': 'white',
                          width: '100px',
                          'margin-left': '100px'
                        }}
                      >
                        Cancel
                      </Button>
                      <Button
                        id='submitted-link'
                        variant='contained'
                        style={{color: 'white',
                          'background-color': '#3ab77d',
                          width: '100px',
                          'margin-left': '10px'
                        }}
                      >
                        Apply
                      </Button>
                    </div>
                  </div>
                </Fade>
              )}
            </Popper>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount']}
          summaryValues={[
            totalTaxableAmount, totalTax, totalAmount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-invoice-number-field' variant='h6' color='textSecondary'>
                INVOICE NO.
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TICKET NO
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-invoice-number-value' variant='h6'>
                {invoiceNumber}
                <RemoveCircleIcon onClick={() => handlBackToSelectTransactionType()} id='view-billing-icon' color='secondary' className={classes['remove-icon']} />
              </Typography>
              <Typography id='content-details-trasaction-date-value'>
                {moment(today).format('MMM DD, YYYY')}

              </Typography>
              <Typography
                id='content-details-trasaction-date-value'
                style={{ width: '100px', 'white-space': 'nowrap', overflow: 'visible' }}
              />
              <Typography id='content-details-ticket-number-value' style={{ width: '100px' }} />
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable pagination={pagination} className={classes['create-invoice-table']} tableHeaders={tableHeaders} tableHeight={'325px'}>
          <TableBody>
            {/* Table Row for fields */}
            <TableRow hover tabIndex={-1}>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <NativeSelect
                  className={classes['select-transaction-type-select-field']}
                  error={false}
                  fullWidth={false}
                  onChange={e => { setTransactionType(e.target.value) }}
                  value={transactionType}

                >
                  <option aria-label='None' value='' />
                  {listOfTransactionType.map((detail, detailIndex) => (
                    <option value={detail.name}>{detail.name}</option>
                  ))}
                </NativeSelect>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <TextField
                  className={classes['create-transaction-text-field']}
                  error={false}
                  id='outlined-basic'
                  variant='outlined'
                  size='small'
                  fullWidth={false}
                  onChange={e => { setMemberAccountId(e.target.value) }}
                  value={memberAccountId}
                />
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <TextField
                  className={classes['create-transaction-text-field']}
                  error={false}
                  id='outlined-basic'
                  variant='outlined'
                  size='small'
                  fullWidth={false}
                  onChange={e => { setMemberName(e.target.value) }}
                  value={memberName}
                />
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <TextField
                  className={classes['create-transaction-text-field']}
                  error={false}
                  id='outlined-basic'
                  variant='outlined'
                  size='small'
                  fullWidth={false}
                  onChange={e => { setDescription(e.target.value) }}
                  value={description}
                />
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <NativeSelect
                  className={classes['select-tax-type-select-field']}
                  error={false}
                  fullWidth={false}
                  onChange={e => { setTaxType(e.target.value) }}
                  value={taxType}
                >
                  <option aria-label='None' value='' />
                  {listOfTaxType.map((detail, detailIndex) => (
                    <option value={detail.name}>{detail.name}</option>
                  ))}
                </NativeSelect>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <TextField
                  className={classes['create-transaction-text-field']}
                  error={false}
                  id='outlined-basic'
                  variant='outlined'
                  size='small'
                  type='number'
                  fullWidth={false}
                  onChange={e => { setTaxableAmount(e.target.value) }}
                  value={taxableAmount}
                />
              </TableCell>
              <TableCell style={{padding: '0px', width: '50px'}}
                className={classes['action-buttons-table-cell']} align='center'
              >
                <Link style={{color: 'green'}} onClick={() => { handleAddField() }}>+ Add</Link>
              </TableCell>
            </TableRow>
            {/* Table row For error message */}
            <TableRow style={{padding: '0px'}} tabIndex={-1}>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <div style={{'font-size': '13px', color: 'red', padding: '0px'}}>{transactionTypeErrorMessage}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <div>{''}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <div>{''}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <div style={{'font-size': '13px', color: 'red'}}>{descriptionErrorMessage}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <div style={{'font-size': '13px', color: 'red'}}>{taxTypeErrorMessage}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <div style={{'font-size': '13px', color: 'red'}}>{taxableAmountErrorMessage}</div>
              </TableCell>
              <TableCell className={classes['create-transaction-table-cell']} align='center'>
                <div>{' '}</div>
              </TableCell>
            </TableRow>
            {fields.map((detail, detailIndex) => (
              <TableRow hover key={detailIndex}>
                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <NativeSelect
                      className={classes['select-transaction-type-select-field']}
                      error={false}
                      fullWidth={false}
                      onChange={e => { setEditTransactionType(e.target.value) }}
                      value={editTransactionType}
                    >
                      <option aria-label='None' value='' />
                      {listOfTransactionType.map((detail, detailIndex) => (
                        <option value={detail.name}>{detail.name}</option>
                      ))}
                    </NativeSelect>
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                }

                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <TextField
                      className={classes['create-transaction-text-field']}
                      error={false}
                      id='outlined-basic'
                      variant='outlined'
                      size='small'
                      fullWidth={false}
                      onChange={e => setEditMemberAccountId(e.target.value)}
                      value={editMemberAccountId}
                    />
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                }

                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <TextField
                      className={classes['create-transaction-text-field']}
                      error={false}
                      id='outlined-basic'
                      variant='outlined'
                      size='small'
                      fullWidth={false}
                      onChange={e => setEditMemberName(e.target.value)}
                      value={editMemberName}
                    />
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberName}
                  </TableCell>
                }

                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <TextField
                      className={classes['create-transaction-text-field']}
                      error={false}
                      id='outlined-basic'
                      variant='outlined'
                      size='small'
                      fullWidth={false}
                      onChange={e => setDescription(e.target.value)}
                      value={editDescription}
                    />
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    {detail.description}
                  </TableCell>
                }
                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <NativeSelect
                      className={classes['select-tax-type-select-field']}
                      error={false}
                      fullWidth={false}
                      onChange={e => { setEditTaxType(e.target.value) }}
                      value={editTaxType}
                    >
                      <option aria-label='None' value='' />
                      {listOfTaxType.map((detail, detailIndex) => (
                        <option value={detail.name}>{detail.name}</option>
                      ))}
                    </NativeSelect>
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='right'
                  >
                    {detail.taxType}
                  </TableCell>
                }
                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                  >
                    <TextField
                      className={classes['create-transaction-text-field']}
                      error={false}
                      id='outlined-basic'
                      variant='outlined'
                      size='small'
                      fullWidth={false}
                      onChange={e => setEditTaxableAmount(e.target.value)}
                      value={editTaxableAmount}
                    />
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                }
                {detail.canEdit
                  ? <TableCell
                    key={detailIndex}
                    align='center'
                    style={{padding: '0px', width: '50px'}}
                  >
                    <Typography style={{color: 'green', 'font-size': '11px', 'margin-bottom': '5px', cursor: 'pointer'}}
                      onClick={e => handleUpdateField(detailIndex)}>
                     Update
                    </Typography>
                    <Typography style={{color: 'red', 'font-size': '11px', cursor: 'pointer'}}
                      onClick={e => handleEditField(detailIndex, false)}>
                      Cancel
                    </Typography>
                  </TableCell>
                  : <TableCell
                    key={detailIndex}
                    align='center'
                    style={{padding: '0px', width: '50px'}}
                  >
                    <FontAwesomeIcon style={{color: 'green'}} icon={faEdit} onClick={() => { handleEditField(detailIndex, true) }} />
                    <RemoveCircleIcon
                      style={{color: 'red', padding: '8px 0 0 0'}}
                      id='view-billing-icon'
                      onClick={() => { handleRemoveField(detailIndex) }} />
                  </TableCell>
                }

              </TableRow>
            ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes.button}>
          <Button
            id='close-button'
            variant='contained'
            color='primary'
            className={classes['button-negative']}
            // onClick={e => handleClose()}
          >
              Close
          </Button>
          <div className={classes['item-right-side']}>
            <Button
              id='save-button'
              variant='contained'
              color='primary'
              className={classes['button-positive']}
              disabled={disabledSave}
              onClick={e => setOpenSaveDialog(true)}
            >
                Save For Now
            </Button>
            <Button
              id='submitted-link'
              variant='contained'
              className={classes['button-positive']}
              // onClick={e => handleSubmitted(transaction_number)}
            >
                Submit
            </Button>
          </div>
        </div>
      </FormDrawer>
      {openSaveDialog === true ? (
        <Submit
          title={'Confirm Saving'}
          confirmationText={'Are you sure you want to save the Invoice?'}
          succesfulTitle={'Invoice Saved Successfully'}
          successfulText={'Invoice was successfully saved, you can edit and submit it by going to the Pending Invoice Tile.'}
          buttonText={'Save'}
          page={'createInvoice'}
          openDialog={openSaveDialog}
          setOpenDialog={setOpenSaveDialog}
          submitFunction={() => handleSaveInvoice()}
        />
      ) : (
        ''
      )}

    </div>
  )
}
