const rangeDate = {
  today: {
    start: moment()
      .startOf('day')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  },
  week: {
    start: moment()
      .startOf('day')
      .subtract(1, 'weeks')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  },
  month: {
    start: moment()
      .subtract(1, 'months')
      .startOf('day')
      .toISOString(),
    end: moment()
      .endOf('day')
      .toISOString()
  }
}

