import React from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

// Components
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import DatePicker from './../../../Components/FormComponents/DatePicker/DatePicker'
import DetailTile from './../../../Components/FormComponents/DetailTile/DetailTile'
import PageFooter from './../../../Components/PageFooter/PageFooter'
import TextField from './../../../Components/FormComponents/TextField/CustomTextField'
import SuspensionBreakDown from '../components/SuspensionBreakDown/SuspensionBreakDown'

// Store
import { useDispatch, useSelector } from 'react-redux'
import { SetSuspensionFields, UpdateSuspensionField } from './../../../Store/actions'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Mock
import details from '../mock-data/submitted-suspension-details.json'
import soaList from '../mock-data/soa-data.json'

// Styles
import classes from './ValidateSuggestedAccount.module.scss'

export default function ValidateSuggestedAccount() {
  const dispatch = useDispatch()
  const data = useSelector(state => state.Suspension)
  const [pageTitle, setPageTitle] = React.useState('Validate Suggested Account for Suspension')
  const [willVoid, setWillVoid] = React.useState(false)
  const [willDisapprove, setWillDisapprove] = React.useState(false)
  const [willApprove, setWillApprove] = React.useState(false)

  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Validate Details', 'Approve'],
    stepNumber: 1
  })

  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    switch (stepper.stepNumber) {
      case 1:
        return <SuspensionBreakDown {...componentProp} />
      case 2:
        return <h1>Approved Page</h1>
      default:
        return ''
    }
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handlePageTitleChange = pageTitle => {
    setPageTitle(pageTitle)
  }

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems(['listOfSubmittedSuspension', 'validateSuggestedAccount'])
    dispatch(SetSuspensionFields({ ...details, soa: soaList }))
  }, [])

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header} direction='row' justify='space-between'>
            <Grid item>
              <Typography variant='h5'>
                {pageTitle}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>

          <Grid container>
            <Grid item xs={12}>
              <Grid container>
                <Grid item xs={12}>
                  <Grid container justify='space-between' spacing={2}>
                    <Grid item xs>
                      <DetailTile label='SPN No.' text={data.spn_number} />
                    </Grid>
                    <Grid item xs>
                      <DetailTile label='Account Name' text={data.account_name} />
                    </Grid>
                    <Grid item xs>
                      <DetailTile label='Account No.' text={data.account_number} />
                    </Grid>
                    <Grid item xs>
                      <DetailTile label='Account TIN' text={data.account_tin} />
                    </Grid>
                    <Grid item xs>
                      <DetailTile label='Broker' text={data.broker} />
                    </Grid>
                  </Grid>
                </Grid>

                <Grid item xs={12}>
                  <Grid container justify='space-between' spacing={2}>
                    <Grid key='effectivity-of-suspension-date' item>
                      <DatePicker
                        id='effectivity-of-suspension'
                        required
                        label='Effectivity of Suspension'
                        value={data.effectivity_of_suspension}
                        onChange={event => {
                          dispatch(
                            UpdateSuspensionField({ field: 'effectivity_of_suspension', value: event.toISOString() })
                          )
                        }}
                        orientation={'column'}
                      />
                    </Grid>
                    <Grid key='reason-for-suspension-key' item xs>
                      <Grid item xs>
                        <TextField
                          id='reason-for-suspension'
                          label='Reason for Suspension'
                          value={data.reason_for_suspension}
                          required
                          onChange={e => {
                            dispatch(
                              UpdateSuspensionField({
                                value: e.target.value,
                                field: 'reason_for_suspension'
                              })
                            )
                          }}
                        />
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    constainerStyle={{ width: 'unset', flex: 1 }}
                    multiline
                    rows={3}
                    id='remarks'
                    label='Remarks'
                    value={data.remarks || ''}
                    onChange={event => {
                      dispatch(UpdateSuspensionField({ field: 'remarks', value: event.target.value }))
                    }}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          {showComponent({ handlePageTitleChange })}
        </Grid>

        <Grid item xs={12}>
          <PageFooter 
            showValidateAccountForSuspensionBtn
            isStepper
            stepNumber={stepper.stepNumber}
            stepCount={stepper.stepDetails.length}
            handleStepChange={handleStepChange}
            handleOnCancelClicked={console.log}
            voidAccountSuspension={willVoid}
            handleVoidAccountSuspension={() => setWillVoid(true)}
            handleVoidAccountSuspensionCancellation={() => setWillVoid(false)}
            handleVoidAccountSuspensionConfirmation={() => setWillVoid(false)}
            disapproveAccountSuspension={willDisapprove}
            handleDisapproveAccountSuspension={() => setWillDisapprove(true)}
            handleDisapproveAccountSuspensionCancellation={() => setWillDisapprove(false)}
            handleDisapproveAccountSuspensionConfirmation={() => setWillDisapprove(false)}
            approveAccountSuspension={willApprove}
            handleApproveAccountSuspension={() => setWillApprove(true)}
            handleApproveAccountSuspensionCancellation={() => setWillApprove(false)}
            handleApproveAccountSuspensionConfirmation={() => setWillApprove(false)}
          />
        </Grid>
      </Grid>
    </div>
  )
}
