export default function BillingAnalystRoutes () {
  const [userRole, setUserRole] = React.useState('')
  // const [isLoading, setIsLoading] = React.useState(true)

  React.useEffect(() => {
    document.title = 'HIMS - Billing'
    const role = localStorage.getItem('role')
    setUserRole(role)
    // setIsLoading(false)
  }, [])

  SideBarService.setItems(sideBarItems)

  if (userRole === 'BILLING_ANALYST') {
    BreadcrumbsService.setItems({ ...breadcrumbsConfig['ANALYST'], ...breadcrumbsConfig['COLLECTION'] })
  } else {
    BreadcrumbsService.setItems({ ...breadcrumbsConfig['SUPERVISOR'], ...breadcrumbsConfig['COLLECTION'] })
  }

  return (
    <Switch>
      <Route path='/' exact component={Dashboard} />
      <Route path='/billing/journal/accounts' exact component={ListOfAccountsPage} />
      <Route path='/billing/journal/transactions/:clientId' exact component={ClientTransactionJournalPage} />
      <Route path='/clients/assign/:_id/:account_number' exact component={AssignAnalystPage} />
      <Route path='/clients/analyze/:_id/:account_number' exact component={BillingCycleSetupPage} />
      <Route path='/billing/invoice/approved/:transaction_number' exact component={InvoiceApprovedPage} />
      {RBAC.hasPolicy('BC-017') && (
        <Route path='/clients/assign/:_id/:account_number' exact component={AssignAnalystPage} />
      )}
      {RBAC.hasPolicy('BC-021') && (
        <Route path='/clients/analyze/:_id/:account_number' exact component={BillingCycleSetupPage} />
      )}
      {RBAC.hasPolicy('BC-001') && <Route path='/clients/ledger/' exact component={ClientTransactionSetupPage} />}
      {RBAC.hasPolicy('BC-005') && <Route path='/billing/ledgers/:soaNumber/:type' exact component={SOAPage} />}
      {RBAC.hasPolicy('BC-005') && (
        <Route path='/billing/ledgers-validated/:soaNumber/:type' exact component={ValidatedSOAPage} />
      )}
      {RBAC.hasPolicy('BC-001') && <Route path='/billing/journal/accounts' exact component={ListOfAccountsPage} />}
      {RBAC.hasPolicy('BC-001') && (
        <Route path='/billing/journal/transactions/:clientId' exact component={ClientTransactionJournalPage} />
      )}
      {/* <Route path='/billing/invoice' exact component={InvoiceApprovedPage} /> */}
      {RBAC.hasPolicy('BC-035') && (
        <Route path='/billing/invoice/pending/:transactionNumber' exact component={InvoicePendingPage} />
      )}
      {RBAC.hasPolicy('BC-035') && (
        <Route
          path='/billing/invoice/forapproval/:transaction_number'
          exact
          component={InvoiceSubmittedForApprovalPage}
        />
      )}
      {RBAC.hasPolicy('BC-035') && (
        <Route path='/billing/invoice/approved/:transaction_number' exact component={InvoiceApprovedPage} />
      )}
      {RBAC.hasPolicy('BC-043') && (
        <Route path='/billing/credit/pending/:transactionNumber' exact component={CreditMemoPendingPage} />
      )}
      {RBAC.hasPolicy('BC-043') && (
        <Route
          path='/billing/credit/forapproval/:transaction_number'
          exact
          component={CreditMemoSubmittedForApprovalPage}
        />
      )}
      {RBAC.hasPolicy('BC-043') && (
        <Route path='/billing/credit/approved/:transaction_number' exact component={CreditMemoApprovedPage} />
      )}
      {RBAC.hasPolicy('BC-051') && (
        <Route path='/billing/debit/pending/:transactionNumber' exact component={DebitMemoPendingPage} />
      )}
      {RBAC.hasPolicy('BC-051') && (
        <Route
          path='/billing/debit/forapproval/:transaction_number'
          exact
          component={DebitMemoSubmittedForApprovalPage}
        />
      )}
      {RBAC.hasPolicy('BC-051') && (
        <Route path='/billing/debit/approved/:transaction_number' exact component={DebitMemoApprovedPage} />
      )}
      {RBAC.hasPolicy('BC-037') && <Route path='/billing/create/transaction' exact component={CreateTransactionPage} />}

      {RBAC.hasPolicy('BC-039') && (
        <Route
          path='/billing/client/invoice/validate/:transaction_number'
          exact
          component={ClientInvoicesToValidatePage}
        />
      )}
      {/* RBAC to Follow */}
      <Route path='/billing/member/invoice/validate/sample' exact component={MemberInvoicesToValidatePage} />
      {RBAC.hasPolicy('BC-047') && (
        <Route
          path='/billing/client/credit/validate/:transaction_number'
          exact
          component={ClientCreditMemosToValidatePage}
        />
      )}
      {RBAC.hasPolicy('BC-055') && (
        <Route
          path='/billing/client/debit/validate/:transaction_number'
          exact
          component={ClientDebitMemosToValidatePage}
        />
      )}
      {/* ...... */}
      <Route path='/billing/soa/member/:soaNumber/:type' exact component={MemberSOAPage} />
      {/* ...... */}
      <Route path='/billing/journal/member/:personId' exact component={MemberTransactionJournalPage} />
      {RBAC.hasPolicy('BC-062') && (
        <Route path='/billing/validated/transactions/:transaction_number' exact component={ValidatedTransactionsPage} />
      )}
      <Route path='/reports' exact component={GenerateReportsPage} />
      <Route path='/billing/soas-for-printing' exact component={SOABatchPrintingPage} />
      <Route path='/billing/soa-pdf/view/:soaNumber' exact component={SOAPDFViewPage} />
      <Route path='/collection/dashboard' exact component={CollectionDashboardSuperPage} />
      <Route path='/collection/dashboard/analyst' exact component={CollectionAnalystDashboard} />
      <Route path='/collection/assign/:_id/:account_number' exact component={ClientSetupViewList} />

      {/* Aging Summary */}
      {RBAC.hasPolicy({ or: ['BC-068', 'BC-069'] }) && (
        <Route path='/collection/agingsummary/accounts' exact component={AccountsAgingSummaryPage} />
      )}
      {RBAC.hasPolicy({ or: ['BC-068', 'BC-069'] }) && (
        <Route path='/collection/agingsummary/members' exact component={MemberListAgingSummaryPage} />
      )}
      {RBAC.hasPolicy({ or: ['BC-068', 'BC-069'] }) && (
        <Route path='/collection/agingsummary/client/:accountNumber' exact component={ClientAgingSummaryPage} />
      )}
      {RBAC.hasPolicy({ or: ['BC-068', 'BC-069'] }) && (
        <Route path='/collection/agingsummary/member/:accountNumber' exact component={MemberAgingSummaryPage} />
      )}
      
      {RBAC.hasPolicy({ or: ['BC-068', 'BC-069'] }) && (
          <Provider store={collectionStore}>
            <Route
              path='/collection/collection-distribution/:data_type/:data_id'
              exact
              component={CollectionDistributionPage}
            />
          </Provider>
      )}

      {RBAC.hasPolicy('BC-070') && (
        <Route path='/collection/lists/account-setup' exact component={CollectionAccountSetupPage} />
      )}
      {RBAC.hasPolicy('BC-101') && (
        <Route path='/collection/lists/reminderletters' exact component={ReminderLettersToSend} />
      )}
      {RBAC.hasPolicy('BC-104') && (
        <Route path='/collection/lists/penalty-endorsements' exact component={PenaltyEndorsementListPage} />
      )}
      {RBAC.hasPolicy('BC-105') && (
        <Route
          path='/collection/lists/endorsed-accounts-penalty'
          exact
          component={EndorsedAccountsForPenaltyListPage}
        />
      )}
      <Route
        path='/collection/validate/advance-distribution'
        exact
        component={ValidateAdvanceCollectionDistributionPage}
      />
      <Route path='/collection/audit/members' exact component={MemberListAgingSummaryPage} />
      {RBAC.hasPolicy('BC-141') && <Route path='/audit-log/:type' exact component={AuditLogPage} />}
      <Provider store={collectionStore}>
        {RBAC.hasPolicy('BC-083') && (
          <React.Fragment>
            {/* <Route
              path='/collection/collection-distribution/:data_type/:data_id'
              exact
              component={CollectionDistributionPage}
            /> */}
            <Route
              path='/collection/create/advance-distribution'
              exact
              component={CreateAdvanceCollectionDistributionPage}
            />
          </React.Fragment>
        )}

        {RBAC.hasPolicy('BC-106') && (
          <Route
            path='/collection/account-penalty/:data_type/:data_id'
            exact
            component={CollectionEndorsedAccountPenaltyPage}
          />
        )}

        {/* TODO: Implement RBAC policies  */}
        <Route path='/collection/suspension/:data_type/:data_id' exact component={SuspensionPage} />
        <Route path='/collection/submitted-suspensions' exact component={SubmittedSuspensionListPage} />
        <Route path='/collection/submitted-suspensions/:data_id' exact component={SubmittedSuspensionDetailsPage} />
        <Route path='/collection/validate-suspension/:data_id' exact component={ValidateSuggestedAccountPage} />
        <Route path='/collection/approved-suspensions' exact component={ApprovedSuspensionListPage} />
        <Route path='/collection/approved-suspensions/:data_id' exact component={ApprovedSuspensionDetailsPage} />

        <Route path='/collection/maintenance' exact component={CollectionMaintenancePage} />
        <Route path='/collection/maintenance/create-letter-template' exact component={CreateLetterTemplatePage} />

        <Route
          path='/collection/distribution/validate/:data_type/:data_id'
          exact
          component={CollectionDistributionToValidatePage}
        />

        {/* COLLECTION LISTS */}
        <Route
          path='/collection/distribution/lists/validate/client'
          exact
          component={ValidateClientListCollectionDistributionPage}
        />
        <Route
          path='/collection/distribution/lists/validate/members'
          exact
          component={ValidateMembersListCollectionDistributionPage}
        />
        <Route path='/collection/validated/lists' exact component={ValidatedListTransactionsPage} />
      </Provider>
      {/* ERROR PAGE (RBAC PRESENTATIONAL). SOON TO BE REFACTOR or REMOVE */}
      {/* <Route path='*' component={ErrorPage} />
      <ErrorPageInit /> */}
      {/* Collection routes
        route - /collection/assign/:_id/:account_number
      */}
      {/* <Route path='/collection/testing' exact component={CollectionAnalystDashboard} /> */}
    </Switch>
  )
}
