import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import BillingCreditMemoServices from 'Services/Api/BillingCreditMemoServices'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from '../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll4/ViewAll'

// Style
import classes from './ClientCreditMemosToValidateTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'week'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(1, 'weeks').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(1, 'months').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}
// const columnConfig = [
//   { id: 'ticket-list-tile-table-header', name: '' },
//   { id: 'ticket-list-tile-table-header', name: 'ID' },
//   { id: 'ticket-list-tile-table-header', name: 'Due Date' },
//   { id: 'ticket-list-tile-table-header', name: 'Account Name' }
// ]

// Mock data
const mock = require('./../mock-data/collection-ticketList.json')

const ClientCreditMemosToValidateTile = props => {
  // Constants
  const { service = () => {} } = props

  const initialValue = {
    show: false,
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filters: null,
    data: [],
    orderBy: '_id',
    order: true,
    sort: ''
  }

  const [params, setParams] = React.useState({})
  const [hasCalled, setHasCalled] = React.useState(false)
  const [tile, setTile] = React.useState(initialValue)
  const [viewList, setViewList] = React.useState(initialValue)

  // Initialize / Watch
  // React.useEffect(() => {
  //   BillingCreditMemoServices.getCreditMemosForApproval().then(res => {
  //     let data = null
  //     try {
  //       data = res.data.data[0]
  //     } catch (error) {
  //       data = []
  //     }
  //     setTile(i => ({ ...i, count: data.length, data }))
  //   })
  // }, [])

  // Methods
  // TODO: Implement if Viewlist otherwise, remove
  // TODO: Call Initial service here for viewlist data
  /**
   * API Call for tile data
   * @param service passed down to component as props for api-service
   * @param get service used to fetch data
   */
  const handleOnShowViewList = () => {
    setViewList({ ...viewList, show: true })
  }

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchData({})
      fetchTileData({ range: rangeDate['month']});
      setHasCalled(true)
    }
  }

  // const fetchData = () => {
  //   console.log('Client Credit Memos to Validate Tile is on viewport')
  //   if (!hasCalled) {
  //     BillingCreditMemoServices.getCreditMemosForApproval().then(res => {
  //       let data = null
  //       try {
  //         data = res.data.data[0]
  //       } catch (error) {
  //         data = []
  //       }
  //       setTile(i => ({ ...i, count: data.length, data }))
  //     })
  //     setHasCalled(true)
  //   }
  // }

  const fetchData = (params) => {
    const _params = {
      limit: viewList.limit,
      page: viewList.page,
      sort: params.sort ? `${params.sort}|${params.order ? 'asc' : 'desc'}` : `${viewList.orderBy}|${viewList.order ? 'asc' : 'desc'}`,
      filters: viewList.filters,
      ...params
    }
    setParams(_params)

    BillingCreditMemoServices.getCreditMemosForApproval(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      console.log(results)
      setViewList(i => ({ ...i, data: results, count, limit, page, orderBy: params.sort || viewList.orderBy , order: params.order || viewList.order }))
    })
  }

  const fetchTileData = (params) => {
    BillingCreditMemoServices.getCreditMemosForApprovalTile(params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      console.log(res.data)
      setTile(i => ({ ...i, data: results, count , limit, page }))
    })
  }

  const onFilterChange = (value) => {
    console.log(value)
    fetchTileData({ range: rangeDate[value]})
  }


  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }

  const ViewportBlock = handleViewport(Block)

  const assignTicket = value => {
    BillingCreditMemoServices.assignSupervisor({ transaction_number: value })
  }

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Client Credit Memos to Validate Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tile.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Client Credit Memos To Validate'}
        isFilterShown
        filterMenu={filterMenu }
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnShowViewList()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Account Name</TableCell>
              <TableCell>Billing Cycle</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tile.data.map((item, index) => (
              <TableRow key={index}>
                <TableCell style={{ paddingLeft: '6px' }}>{item.client.account_name}</TableCell>
                <TableCell>{item.billing.cycle.code}</TableCell>
                <TableCell>
                  <Link
                    to={`billing/client/credit/validate/${item.transaction.number}`}
                    onClick={() => assignTicket(item.transaction.number)}
                  >
                    <Typography variant='caption' className='text-link underline'>
                      Validate
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'client-credit-memos-to-validate-modal'}
        config={config}
        tile={'clientCreditMemosToValidate'}
        type='Client Credit Memos To Validate'
        title='List of Client Credit Memos To Validate'
        placeholder='Search List of Client Credit Memos To Validate'
        contents={viewList.data}
        count={viewList.count}
        viewAll={viewList}
        setViewAll={setViewList}
        params={params}
        accessors={['transaction.number', 'transaction.type', 'client.account_name', 'transaction.date', 'submitted_by']}
        fetchData={fetchData}
        linkCaption='Validate'
      />
    </React.Fragment>
  )
}

ClientCreditMemosToValidateTile.displayName = 'DashboardTileComponent'

export default ClientCreditMemosToValidateTile
