import Grid from '@material-ui/core/Grid'
import React from 'react'
import { Typography, IconButton, Button, MenuItem, Menu, ListItemText, TableRow, TableCell } from '@material-ui/core'

// Icons
import KeyboardArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import AddIcon from '@material-ui/icons/Add'

// Components
import DetailTile from './../../../../Components/FormComponents/DetailTile/DetailTile'
import FormSlider from './../../../../Components/FormSlider/FormSlider'
import { CustomTable } from './../../../../Components/CustomTable/index'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Store
import { useSelector } from 'react-redux'
// import { SetEndorsedPenalty, UpdateEndorsedPenalty } from '../../../../Store/actions'

// Styles
// import classes from './AnalyzeDetailsComponent.module.scss'
import { withStyles, makeStyles } from '@material-ui/core/styles'

// Mock
const tableBody = require('../../mock-data/table-data.json')
const headCells = require('../../mock-data/table-head.js')

const useStyles = makeStyles(theme => ({
  title: {
    fontSize: '16px',
    fontWeight: 'bold'
  },
  details: {
    fontSize: '14px'
  }
}))

const StyledMenu = withStyles({
  paper: {
    border: '1px solid #d3d4d5'
  }
})(props => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center'
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center'
    }}
    {...props}
  />
))

const StyledMenuItem = withStyles(theme => ({
  root: {
    '&:focus': {
      backgroundColor: theme.palette.primary.main,
      '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
        color: theme.palette.common.white
      }
    }
  }
}))(MenuItem)

export default function AnalyzeDetailsComponent (props) {
  // stored data
  const _data = useSelector(state => state.EndorsedAccountPenalty)

  const [_state, _setState] = React.useState({
    body: tableBody,
    count: 20,
    filter: {},
    header: headCells.headCells,
    limit: 1,
    order: true,
    orderBy: 'account_no',
    page: 1,
    sort: ''
  })

  const [activeSlider, setActiveSlider] = React.useState(0)
  const [anchorEl, setAnchorEl] = React.useState(null)

  // Mui-Theming
  const muiClasses = useStyles()

  // Dashboard -> Aging Summary -> Endorsed
  React.useEffect(() => {
    BreadcrumbsService.setSelectedItems([
      'collectionDistributionDashboard',
      'listOfAccountsWithAgingSummary',
      `clientAgingSummary:${'STAR MUSIC'}`,
      'endorseAccountForPenalty'
    ])
  })

  // Methods
  const onClickBtnSOA = e => {
    setAnchorEl(e.currentTarget)
  }
  const onCloseBtnSOA = e => {
    setAnchorEl(null)
  }

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container justify='space-between' spacing={2}>
          <Grid key={'pn-no'} item>
            <DetailTile id={'pn-no'} label={'pn no.'} text={_data.penalty_number} />
          </Grid>
          <Grid key={'account-name'} item>
            <DetailTile label={'account name'} text={_data.account_name} />
          </Grid>
          <Grid key={'account-no'} item>
            <DetailTile label={'account no.'} text={_data.account_number} />
          </Grid>
          <Grid key={'account-tin'} item>
            <DetailTile label={'account tin'} text={_data.account_tin} />
          </Grid>
          <Grid key={'broker'} item>
            <DetailTile label={'broker'} text={_data.broker} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Typography variant='h5'>Penalty Reference</Typography>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={12} md={10} lg={10}>
            <Grid container>
              <FormSlider
                details={_data && _data.soa}
                activeSlider={activeSlider && activeSlider}
                // removeData={}
              />
            </Grid>
          </Grid>
          <Grid item xs={12} sm={12} md={2} lg={2}>
            <Grid container>
              <Grid style={{ minHeight: '50px' }}>
                {(activeSlider && activeSlider) > 0 ? (
                  <IconButton onClick={() => setActiveSlider(activeSlider - 1)}>
                    <KeyboardArrowLeftIcon />
                  </IconButton>
                ) : (
                  ''
                )}
              </Grid>
              <Grid style={{ minHeight: '50px' }}>
                <IconButton onClick={() => setActiveSlider(activeSlider + 1)}>
                  <KeyboardArrowRightIcon />
                </IconButton>
              </Grid>
            </Grid>
            <Grid container style={{ justifyContent: 'flex-end' }}>
              <Button style={{ background: '#3ab77d', color: '#fff' }} onClick={onClickBtnSOA}>
                <AddIcon style={{ fontSize: '12px' }} />
                <Typography style={{ paddingLeft: '5px' }}>ADD SOA</Typography>
              </Button>
              <StyledMenu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={onCloseBtnSOA}>
                {tableBody &&
                  tableBody.map((data, key) => (
                    <StyledMenuItem key={key}>
                      <ListItemText>
                        <Grid>
                          <Typography className={muiClasses.title}>{data.member_id}</Typography>
                        </Grid>
                        <Grid>
                          <Typography className={muiClasses.details}>{data.first_name}</Typography>
                        </Grid>
                        <Grid>
                          <Typography className={muiClasses.details}>{data.last_name}</Typography>
                        </Grid>
                      </ListItemText>
                    </StyledMenuItem>
                  ))}
              </StyledMenu>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12} style={{ marginTop: '1.5rem' }}>
        <CustomTable
          // hasLink
          data={_state}
          setData={_setState}
          showTextField
          body={_data =>
            _data.map(({ member_id, last_name, first_name, middle_initial, ref_date, end_date, plan_type }, index) => (
              <TableRow key={`detail-list-item-${index}`}>
                <TableCell id={`detail-id-${index}`}>{index + 1}</TableCell>
                <TableCell id={`detail-member-id-${index}`}>{member_id}</TableCell>
                <TableCell id={`detail-last-name-${index}`}>{last_name}</TableCell>
                <TableCell id={`detail-first-name-${index}`}>{first_name}</TableCell>
                <TableCell id={`detail-middle-initial-${index}`}>{middle_initial}</TableCell>
                <TableCell id={`detail-ref-date-${index}`}>{ref_date}</TableCell>
                <TableCell id={`detail-end-date-${index}`}>{end_date}</TableCell>
                <TableCell id={`detail-contract-type-${index}`}>{plan_type}</TableCell>
              </TableRow>
            ))
          }
        />
      </Grid>
    </Grid>
  )
}
