import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import BillingDebitMemoServices from 'Services/Api/BillingDebitMemoServices'
import handleViewport from 'react-in-viewport'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll4/ViewAll'

// Style
import classes from './PendingDebitMemosTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' }
  ],
  default: 'month'
}

const rangeDate = {
  'today': { start: moment().startOf('day').toISOString(), end: moment().endOf('day').toISOString() },
  'week': { start: moment().startOf('day').subtract(1, 'weeks').toISOString(), end: moment().endOf('day').toISOString() },
  'month': { start: moment().subtract(1, 'months').startOf('day').toISOString(), end: moment().endOf('day').toISOString() }
}

const PendingDebitMemosTile = props => {
  // Constants
  const { service = () => {} } = props

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props
    const text = inViewport ? 'In viewport' : 'Not in viewport'
    return <div className='viewport-block' ref={forwardedRef}></div>
  }
  const ViewportBlock = handleViewport(Block)

  const initialValue = {
    body: [],
    count: 0,
    filters: null,
    header: [],
    limit: 10,
    order: true,
    orderBy: '_id',
    page: 1,
    sort: ''
  }

  const [isFetching, setIsFetchingState] = React.useState(false)
  const [hasCalled, setHasCalled] = React.useState(false)
  const [list, setList] = React.useState(initialValue)
  const [tileList, setTileList] = React.useState(initialValue)
  const [show, setShow] = React.useState(false)
  const [params, setParams] = React.useState({})

  const viewPortGetData = () => {
    if (!hasCalled) {
      fetchTileData({ range: rangeDate['month']});
      setHasCalled(true)
    }
  }

  const fetchData = params => {
    setIsFetchingState(true)
    const _params = {
      limit: list.limit,
      page: list.page,
      sort: `${list.orderBy}|${list.order ? 'asc' : 'desc'}`,
      filters: list.filters,
      ...params
    }
    setParams(_params)

    BillingDebitMemoServices.getDebitMemosNew(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setList(i => ({ ...i, body: results, count, limit, page }))
      setIsFetchingState(false)
    })
  }

  const fetchTileData = (params) => {
    BillingDebitMemoServices.getDebitMemosNewTile(params).then(({ data: res }) => {
      const { results, count } = res.data
      setTileList(i => ({ ...i, body: results, count }))
    })
  }

  const onFilterChange = (value) => {
    fetchTileData({ range: rangeDate[value]})
  }

  // Methods
  // TODO: Implement if Viewlist otherwise, remove
  // TODO: Call Initial service here for viewlist data
  /**
   * API Call for tile data
   * @param service passed down to component as props for api-service
   * @param get service used to fetch data
   */

  const assignTicket = value => {
    BillingDebitMemoServices.assignAnalyst({ transaction_number: value })
  }

  return (
    <React.Fragment>
      <ViewportBlock
        onEnterViewport={() => viewPortGetData()}
        onLeaveViewport={() => console.log('Pending Debit Memos Tile is not on viewport')}
      />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 7, value: tileList.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={'Pending Debit Memos'}
        isFilterShown
        filterMenu={filterMenu}
        onFilterChange={onFilterChange}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => { setShow(true); fetchData({}) } }
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{ paddingLeft: '6px' }}>Transaction No.</TableCell>
              <TableCell>Billing Cycle</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tileList.body && tileList.body.map((item, index) => (
              <TableRow>
                <TableCell style={{ paddingLeft: '6px' }}>{item.transaction.number}</TableCell>
                <TableCell>{item.billing_client_contract[0].billing.cycle.code}</TableCell>
                <TableCell align={'right'} style={{ paddingRight: '0' }}>
                  <Link
                    to={`billing/debit/pending/${item.transaction.number}`}
                    onClick={() => assignTicket(item.transaction.number)}
                  >
                    <Typography variant='caption' className='text-link underline'>
                      Edit
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'pending-debit-memos-modal'}
        config={config}
        tile={'pendingDebitMemos'}
        type='Pending Debit Memos'
        title='List of Pending Debit Memos'
        contents={list.body}
        count={list.count}
        list={list}
        show={show}
        setShow={setShow}
        fetchData={fetchData}
        params={params}
        isFetching={isFetching}
        accessors={['transaction.number', 'billing_client_contract.client.account_name']}
      />
    </React.Fragment>
  )
}

PendingDebitMemosTile.displayName = 'DashboardTileComponent'

export default PendingDebitMemosTile
