import React from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { config } from '../ViewAllConfig'
import handleViewport from 'react-in-viewport'

// Components
import DashboardTile from './../../DashboardTileComponent/DashboardTileComponent'
import ViewAll from '../../ViewAll2/ViewAll'

// Style
import classes from './ClientTPAsToBeReplenishedTile.module.scss'

// Utilities
import moment from 'moment'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'week', text: 'This Week' },
    { value: 'month', text: 'This Month' },
    { value: 'showAll', text: 'Show All' }
  ],
  default: 'showAll'
}

// const columnConfig = [
//   { id: 'ticket-list-tile-table-header', name: '' },
//   { id: 'ticket-list-tile-table-header', name: 'ID' },
//   { id: 'ticket-list-tile-table-header', name: 'Due Date' },
//   { id: 'ticket-list-tile-table-header', name: 'Account Name' }
// ]

const ClientTPAsToBeReplenishedTile = props => {
  // Constants
  const { services = () => {} } = props

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'name',
    order: true
  }

  const [hasCalled, setHasCalled] = React.useState(false)
  const [tile, setTile] = React.useState({
    count: 0,
    data: [],
    filterMenu,
    title: 'Clients TPAs to be Replenished',
    variant: 'h6'
  })

  const [viewList, setViewList] = React.useState({
    count: 0,
    data: [],
    show: false,
    title: 'List of TPA Accounts'
  })

  // Initialize / Watch
  // React.useEffect(() => {
  //   services.getTpaAccounts().then(res => {
  //     let data = null
  //     try {
  //       data = res.data.data
  //     } catch (error) {
  //       data = []
  //     }
      // console.log('??????????????????????', data)
  //     setTile(i => ({ ...i, count: data.length, data }))
  //   })
  // }, [])

  const handleOnShowViewList = () => {
    setViewList({ ...initialValue, show: true, title: 'List of TPA Accounts' })
  }

  const fetchData = () => {
    if(!hasCalled){
      console.log('Clients TPAs to be Replenished Tile is on viewport')
      services.getTpaAccounts().then(res => {
        let data = null
        try {
          data = res.data.data
        } catch (error) {
          data = []
        }
        // console.log('??????????????????????', data)
        setTile(i => ({ ...i, count: data.length, data }))
      })
      setHasCalled(true)
    }
  }

  const Block = (props: { inViewport: boolean }) => {
    const { inViewport, forwardedRef } = props;
    const text = inViewport ? 'In viewport' : 'Not in viewport';
    return (
      <div className="viewport-block" ref={forwardedRef}></div>
    )
  }

  const ViewportBlock = handleViewport(Block)

  return (
    <React.Fragment>
      <ViewportBlock onEnterViewport={() => fetchData()} onLeaveViewport={() => console.log('Clients TPAs to be Replenished Tile is not on viewport')} />
      <DashboardTile
        id='ticket-list-tile'
        count={{ color: 6, value: tile.count, id: 'ticket-list-tile-count' }}
        sizes={{ sm: 12 }}
        title={tile.title}
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        viewList={
          <Typography
            id='ticket-list-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnShowViewList()}
          >
            View All
          </Typography>
        }
      >
        <Table stickyHeader size='small'>
          <TableHead>
            <TableRow>
              <TableCell style={{paddingLeft: '6px'}}>Account Name</TableCell>
              <TableCell>Budget</TableCell>
              <TableCell>Date Last Billed</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody className={classes.root}>
            {tile.data.map((item, index) => (
              <TableRow>
                <TableCell style={{padding: '0'}}>{item.client.account_name}</TableCell>
                <TableCell>
                  {parseInt(((item.contract.tpa.rsd - item.contract.tpa.rsd_usage) / item.contract.tpa.rsd) * 100) +
                    '%'}
                </TableCell>
                <TableCell>{item.invoice ? moment(item.invoice.date.created).format('MMM DD, YYYY') : ''}</TableCell>
                <TableCell align={'right'} style={{paddingRight: '0'}}>
                  <Link to={`/`}>
                    <Typography variant='caption' className='text-link underline'>
                      View Journal
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>
      <ViewAll
        id={'tps-to-be-replenished'}
        config={config}
        tile={'tpaMonitoring'}
        type='TPA Monitoring'
        title='List of TPA Accounts'
        contents={tile.data}
        viewAll={viewList}
        setViewAll={setViewList}
        count={viewList.count}
        linkCaption='View Journal'
        passProperty={''}
        route='/'
      />
    </React.Fragment>
  )
}

ClientTPAsToBeReplenishedTile.displayName = 'DashboardTileComponent'

export default ClientTPAsToBeReplenishedTile
