import React from 'react'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import { Link } from 'react-router-dom'

import { Popover, Grid } from '@material-ui/core'

// Components
import Table from 'SharedComponents/Table/Table'

// Styles
import classes from './ViewMore.module.scss'

// const headCells = [
//   { id: 'accounts', numeric: false, center: false, disablePadding: true, label: 'Assigned Accounts' },
//   { id: 'terms', numeric: false, center: true, disablePadding: true, label: 'Payment Terms' },
//   { id: 'cycle', numeric: false, center: false, disablePadding: true, label: 'Billing Cycle' }
// ]
const headCells = [{ name: 'Assigned Accounts' }, { name: 'Payment Terms' }, { name: 'Billing Cycle' }]

export default function CustomizedDialogs (props) {
  const { viewMore, setViewMore, onViewMoreChange } = props

  return (
    <Popover
      open={viewMore.show}
      anchorEl={viewMore.show}
      onClose={() => setViewMore(false)}
      anchorOrigin={{
        vertical: 'center',
        horizontal: 'right'
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right'
      }}
    >
      <div style={{ padding: '1rem' }}>
        <Grid container className={classes.header}>
          <Grid container justify='flex-end'>
            <IconButton aria-label='close' className={classes.button} onClick={() => setViewMore(false)}>
              <CloseIcon />
            </IconButton>
          </Grid>
          <Grid>
            <Typography variant='h6' className={classes.title} style={{ marginBottom: '10px' }}>
              {viewMore.title}
            </Typography>
            <Typography variant='subtitle1' className={classes.subtitle} style={{ marginBottom: '10px' }}>
              {viewMore.subtitle}
            </Typography>
          </Grid>
        </Grid>
        <Table
          TableContainerProps={{ style: { height: '320px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {/* headCells.map() */}
              {['Assigned Accounts', 'Payment Terms', 'Billing Cycle'].map((headCell, index) => (
                <TableCell key={index}>
                  {/* <TableSortLabel
                          active={viewList.orderBy === headCell.sortField}
                          direction={viewList.order ? 'asc' : 'desc'}
                          onClick={handleOnSort(headCell.sortField)}
                        > */}
                  {headCell}
                  {/* </TableSortLabel> */}
                </TableCell>
              ))}
            </TableRow>
          }
          body={
            viewMore.data === undefined
              ? ''
              : viewMore.data.map((row, index) => (
                <TableRow key={index}>
                  <TableCell>{row.client.account_name}</TableCell>
                  <TableCell>{row.contract.payment_terms}</TableCell>
                  <TableCell>
                    {row.billing.cycle.code ? row.billing.cycle.code + ' - ' + row.billing.cycle.name : ''}
                  </TableCell>
                </TableRow>
              ))
          }
          count={viewMore.count}
          page={viewMore.page}
          limit={viewMore.limit}
          rowsPerPageOptions={[10, 15, 20]}
          // onChange={onViewListChange}
        />
      </div>
    </Popover>
  )
}
