// React
import React from 'react'
import { useHistory } from 'react-router-dom'

// Material
import Button from '@material-ui/core/Button'
import { Checkbox, Grid, TableCell, TableRow, Typography } from '@material-ui/core'

// Components
import ControlList from './../../../Components/FormComponents/ControlList/ControlList'
import { CustomTable as Table } from 'Pages/Protected/BillingAndCollection/Components/CustomTable'
import SearchTextComponent from 'Pages/Protected/BillingAndCollection/Components/FormComponents/SearchTextComponent/SearchTextComponent'
import CollectibleHeader from 'SharedComponents/CollectibleHeader/CollectibleHeaderComponent'
import SOAModal from 'Pages/Protected/BillingAndCollection/Components/SOAModal/SOAModal'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionClientLedgerServices from 'Services/Api/CollectionClientLedgerServices'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import CollectionClientContractServices from 'Services/Api/CollectionClientContractServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import moment from 'moment'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

// Config
import header from './config/config'

const ClientAgingSummaryPage = props => {
  const { accountNumber = '7409-5462-2115' } = props.match.params
  const history = useHistory()
  const [open, setOpen] = React.useState(false)
  const [selectedSoa, setSelectedSoa] = React.useState({})
  const [totalCollectibles, setTotalCollectibles] = React.useState([])
  const [soa, setSoa] = React.useState(null)
  const [state, setState] = React.useState({
    body: [],
    count: 20,
    filter: {},
    header: header,
    limit: 10,
    order: true,
    orderBy: 'soa_date',
    page: 1,
    sort: ''
  })

  React.useEffect(() => {
    LoadingService.loading(true)
    Promise.all([CollectionClientLedgerServices.getClientDetails({ accountNumber }), CollectionClientContractServices.getClientContract({accountNumber})])
      .then(res => {
        const [data, contract] = res
        let { results, count } = data.data.data
        const { client } = contract.data.data

        results = results.length > 0 ? results : []
        count = count > 0 ? count : 0

        PageTitleService(`${client.account_name || ''} - Aging Summary`)
        BreadcrumbsService.setSelectedItems(['listOfAccountsWithAgingSummary', `clientAgingSummary:${client.account_name || ''}`])
        setState(i => ({ ...i, body: results, count }))

        for (let i = 0; i < (results && results.length); i++) {
          let temp = {}
          if ((results && results[i].days_overdue && results[i].days_overdue) <= 45) {
            let total_amount = results
              .map(o => o.total_amount_due)
              .reduce((a, c) => {
                return a + c
              })
            temp = {
              total_amount: total_amount
            }
            setTotalCollectibles(i => ({ ...i, ...temp }))
          }
        }
      })
      .catch(error => {
        console.error(`Error Fetching SOA(s): ${error}`)
      })
    LoadingService.loading(false)
  }, [accountNumber])

  console.log('total', totalCollectibles)
  // Methods
  const displaySettingsHandler = () => {
    console.log('display settings clicked')
  }

  const handleClose = () => {
    setOpen(false)
  }
  const handleClickOpen = soa_no => {
    setSoa(soa_no)
    setOpen(true)
  }

  const handlePregenerateCDF = () => {
    LoadingService.loading(true)
    const _soa = []
    Object.keys(selectedSoa).map(i => {
      if (selectedSoa[i]) {
        _soa.push(i)
      }
    })
    // Will not proceed if there's no soa selected
    if (_soa.length === 0) {
      LoadingService.loading(false)
      return false
    }

    CollectionCDFServices.pregenerateCDF({ data: { accountNumber, soa: _soa } })
      .then(res => {
        LoadingService.loading(false)
        const { cdfNumber } = res.data.data
        history.push(`/collection/collection-distribution/client/${cdfNumber}`)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(err)
      })
  }

  const handleInlinePregenerateCDF = params => {
    LoadingService.loading(true)

    CollectionCDFServices.pregenerateCDF({ data: { accountNumber, soa: [params] } })
      .then(res => {
        LoadingService.loading(false)
        const { cdfNumber } = res.data.data
        history.push(`/collection/collection-distribution/client/${cdfNumber}`)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(err)
      })
  }

  const handlePregenerateSuspensionEndorsement = () => {
    // TODO: Determine if an API call is needed

    history.push(`/collection/suspension/client/1`)
  }

  const onCheckboxChanged = params => {
    const { soa_number, value } = params
    setSelectedSoa(i => ({ ...i, [soa_number]: value }))
  }

  const countDaysOverdue = date => {
    let temp = moment(date).format('ll')

    var given = moment(temp, 'll')
    var current = moment().startOf('day')

    // count number of days
    let count = moment.duration(given.diff(current)).asDays()

    if (count < 0) {
      return 'Overdue'
    }

    return count
  }

  const renderBody = _data =>
    (state.limit > 0
      ? _data.slice((state.page - 1) * state.limit, (state.page - 1) * state.limit + state.limit)
      : _data
    ).map(({ due_date, days_overdue, total_amount_due, soa_date, soa_number, i }) => (
      <TableRow key={`aging-summary-table-row-${soa_number}`}>
        <TableCell id={`account-no-${i}`}>
          <Checkbox
            onChange={e => {
              onCheckboxChanged({ soa_number, value: e.target.checked })
            }}
            inputProps={{ 'aria-label': 'primary checkbox' }}
          />
        </TableCell>
        <TableCell id={`soa-date-${i}`}>{moment(soa_date).format('ll')}</TableCell>
        <TableCell id={`soa-no-${i}`}>
          <Button onClick={() => handleClickOpen(soa_number)}>
            <Typography style={{ color: '#48b77c', textDecoration: 'underline' }}>{soa_number}</Typography>
          </Button>
        </TableCell>
        <TableCell id={`due-date-${i}`}>{moment(due_date).format('ll')}</TableCell>
        <TableCell id={`days-overdue-${i}`}>{days_overdue}</TableCell>
        <TableCell id={`total-amount-${i}`}>{total_amount_due}</TableCell>
        <TableCell id={`account-no-${i}`}>
          <Typography
            id={`account-no-${i}`}
            variant='caption'
            className={`text-link underline`}
            onClick={i => {
              handleInlinePregenerateCDF(soa_number)
            }}
          >
            Create CD
          </Typography>
        </TableCell>
      </TableRow>
    ))

  const searchChangeHandler = _data => {
    console.log('search change handler: ' + _data)
  }

  const onDataChange = params => {
    console.log(`onDataListChange: ${JSON.stringify(params)}`)
    const { page } = params
    setState({ ...state, page })
  }

  const searchProps = {
    placeholder: 'Search Accounts',
    displaySettings: true,
    handleOnDisplaySettingsClicked: displaySettingsHandler,
    onChange: searchChangeHandler,
    exportFiles: true
  }

  const tableProps = {
    showCheckBox: true,
    data: state,
    setData: setState,
    body: renderBody,
    hasLink: true,
    onDataListChange: onDataChange
  }

  // TODO - Remove mock data, refactor from data from service
  const collectibles = {
    label: 'TOTAL COLLECTIBLES',
    amount: 1000000.0,
    data: [
      {
        amount: 135000.0,
        period: '1 - 45 DAYS'
      },
      {
        label: 'OVERDUE',
        amount: 135000.0,
        period: '46 - 75 DAYS'
      },
      {
        amount: 125000.0,
        period: '76 - 105 DAYS'
      },
      {
        amount: 110000.0,
        period: '106 - 136 DAYS'
      },
      {
        amount: 179000.0,
        period: 'perio137 - 166 DAYS'
      },
      {
        amount: 130000.0,
        period: '167 - 196 DAYS'
      },
      {
        amount: 172000.0,
        period: 'OVER 197 DAYS'
      }
    ]
  }

  return (
    <React.Fragment>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <SearchTextComponent {...searchProps} />
        </Grid>
        <Grid item xs={12}>
          <CollectibleHeader {...collectibles} />
        </Grid>
        <Grid item xs={12}>
          <ControlList
            createCollectionDistribution
            endorseForPenalty
            suggestForSuspension
            handleOnCreateCollectionDistributionClicked={() => handlePregenerateCDF()}
            handleOnSuggestForSuspensionClicked={handlePregenerateSuspensionEndorsement}
          />
        </Grid>
        <Grid item xs={12}>
          <Table {...tableProps} />
        </Grid>
      </Grid>
      <SOAModal {...{ open, handleClose, soa }} />
    </React.Fragment>
  )
}

export default ClientAgingSummaryPage
